import "./TournamentStructure.css"
import useCtx from "../../redux/context/useCtx"
import StageCreation from "../StageCreation/StageCreation"
import StageForm from "../StageForm/StageForm"

const TournamentStructure = (props) => {
	const { ctx } = useCtx()

	return (
		<>
			{
				props?.tournament?.stages_count > 0 && !ctx?.createStage ?
					<StageForm
						stageNumber={props?.stageNumber}
						tournament={props?.tournament}
					/>
				:
					<StageCreation tournament={props?.tournament} />
			}
		</>
	)
}

export default TournamentStructure