import "./TournamentParticipants.css"

import { useState, useEffect } from "react"
import { useNavigate, useParams } from "react-router-dom"
import { useTranslation } from "react-i18next"
import {
	useAddSinglePlayersMutation,
	useRemoveParticipantMutation,
	useAddDoublesMutation,
	useUpdateDoubleParticipantMutation,
	useRemoveDoubleMutation
} from "../../redux/singleTournamentService/singleTournamentApiSlice"
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd"
import { findPlayers, checkEquality } from "../../generalExternalFunctions"
import {
	filterPlayersList,
	formDeleteRequest,
	formPatchRequest,
	formPostRequestForDoubles,
	formPostRequestForSingles,
	generateRandomUid
} from "./externalFunctions"
import { useDispatch, useSelector } from "react-redux"
import { setParticipants, addParticipant, updateParticipant } from "../../redux/tournamentParticipantsService/tournamentParticipantsSlice"
import { useGetPlayersQuery } from "../../redux/playersService/playersApiSlice"

import { ReactComponent as ButtonDelete } from "../../icon/minus-delete.svg"
import { ReactComponent as Edit } from "../../icon/edit.svg"
import Button from "../../components/Button/Button"
import Popup from "../../components/Popup/Popup"
import Input from "../../components/Input/Input"
import ButtonWhite from "../../components/ButtonWhite/ButtonWhite"

const TournamentParticipants = (props) => {
	const [addSinglePlayers] = useAddSinglePlayersMutation()
	const [removeParticipant] = useRemoveParticipantMutation()
	const [updateDouble] = useUpdateDoubleParticipantMutation()
	const [addDouble] = useAddDoublesMutation()
	const [removeDouble] = useRemoveDoubleMutation()
	const { t } = useTranslation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const { tournamentUid, communityUid } = useParams()
	const participants = useSelector(state => state.participants.participants)
	const { data: communityPlayers = [], isLoading } = useGetPlayersQuery({ community_uid: communityUid })

	const [singlePlayerSearchValue, setSingleInputValue] = useState("")
	const [searchPlayersList, setSearchPlayersList] = useState([])
	const [popupIsOpen, setPopupIsOpen] = useState(false)
	const [firstPlayerInDouble, setFirstPlayerInDouble] = useState("")
	const [secondPlayerInDouble, setSecondPlayerInDouble] = useState("")
	const [chosenSearchInput, setChosenSearchInput] = useState("")
	const [doubleParticpant, setDoubleParticipant] = useState([])
	const [updatingDoubleParticipant, setUpdatingDoubleParticipant] = useState({})
	const [editeDouble, setEditeDouble] = useState(false)
	const [immuntableState, setImmutableState] = useState([])
	const [communityPlayerslist, setCommunityPlayersList] = useState([])
	const [showButtonSaveList, setShowButtonSaveList] = useState(false)
	const [deleteRequest, setDeleteRequest] = useState(false)
	const [postRequest, setPostRequest] = useState(false)
	const [patchRequest, setPatchRequest] = useState(false)

	useEffect(() => {
		if (Object.keys(props?.tournament).length !== 0) {
			setImmutableState(props?.tournament?.players || props?.tournament?.doubles)
			dispatch(setParticipants(props?.tournament?.players || props?.tournament?.doubles))
		}
	}, [props?.tournament])

	useEffect(() => {
		if (communityPlayers?.length > 0 && participants?.length > 0) {
			const filteredPlayersList = filterPlayersList(communityPlayers, participants, props?.tournament?.type)
			setCommunityPlayersList(filteredPlayersList)
			setSearchPlayersList(filteredPlayersList)
		}
	}, [participants, communityPlayers])

	useEffect(() => {
		const isEqual = checkEquality(participants, immuntableState)
		if (isEqual) {
			setShowButtonSaveList(false)
		} else {
			setShowButtonSaveList(true)
		}
	}, [participants, immuntableState])

	if (isLoading) { return <h1>Loading</h1> }

	//---------------- popup functions-------------------

	const closePopup = () => {
		setPopupIsOpen(false)
		setSingleInputValue("")
		setFirstPlayerInDouble("")
		setSecondPlayerInDouble("")
	}

	const search = (value) => {
		if (value !== "") {
			const foundPlayers = findPlayers(value, communityPlayerslist)
			setSearchPlayersList(foundPlayers)
		} else {
			setSearchPlayersList(communityPlayerslist)
		}
	}

	const handleSinglePlayerInput = (value) => {
		setSingleInputValue(value)
		search(value)
	}

	const handleFirstPlayerInDoubleInput = (value) => {
		setFirstPlayerInDouble(value)
		search(value)
	}

	const handleSecondPlayerInDoubleInput = (value) => {
		setSecondPlayerInDouble(value)
		search(value)
	}

	const handlePlayerInPopupList = (el) => {
		if (props?.tournament?.type === "singles") {
			dispatch(addParticipant({ ...el, new: true }))
			setPostRequest(true)
		} else if (props?.tournament?.type === "doubles") {
			addPlayerInDoubleInput({ ...el, new: true })
			setPostRequest(true)
		}
	}

	const automaticalAdditionInInputs = (player) => {
		if (chosenSearchInput === "" && firstPlayerInDouble === "") {
			setFirstPlayerInDouble(`${player?.last_name} ${player?.first_name}`)
			setDoubleParticipant(prev => [player, ...prev.slice(0, 1)])
			return
		}
		if (chosenSearchInput === "" && firstPlayerInDouble !== "") {
			setSecondPlayerInDouble(`${player?.last_name} ${player?.first_name}`)
			setDoubleParticipant(prev => [prev[0], player])
			return
		}
	}

	const manualAdditionInInput = (player) => {
		if (chosenSearchInput === 1) {
			setFirstPlayerInDouble(`${player?.last_name} ${player?.first_name}`)

			if (editeDouble) {
				setUpdatingDoubleParticipant({
					...updatingDoubleParticipant,
					updated: true,
					player1: {
						uid: player?.uid,
						last_name: player?.last_name,
						first_name: player?.first_name
					}
				})
			} else {
				setDoubleParticipant(prev => [player, ...prev.slice(1)])
			}
		} else if (chosenSearchInput === 2) {
			setSecondPlayerInDouble(`${player?.last_name} ${player?.first_name}`)

			if (editeDouble) {
				setUpdatingDoubleParticipant({
					...updatingDoubleParticipant,
					updated: true,
					player2: {
						uid: player?.uid,
						last_name: player?.last_name,
						first_name: player?.first_name
					}
				})
			} else {
				setDoubleParticipant(prev => [prev[0], player, ...prev.slice(2)])
			}
		}
	}

	const addPlayerInDoubleInput = async (player) => {
		automaticalAdditionInInputs(player)
		manualAdditionInInput(player)
	}

	const handleInputOfDoublePopup = async (value) => {
		setChosenSearchInput(value)
		const filteredPlayersList = filterPlayersList(communityPlayers, participants, props?.tournament?.type)
		setSearchPlayersList(filteredPlayersList)
	}

	const handleSaveDoubleParticipant = () => {
		if (!editeDouble) {
			const addingDouble = {
				new: true,
				uid: generateRandomUid(),
				player1: doubleParticpant[0],
				player2: doubleParticpant[1]
			}

			dispatch(addParticipant(addingDouble))
			setFirstPlayerInDouble("")
			setSecondPlayerInDouble("")
			setChosenSearchInput("")
			setPostRequest(true)
		} else if (editeDouble) {
			dispatch(updateParticipant(updatingDoubleParticipant))
			setFirstPlayerInDouble("")
			setSecondPlayerInDouble("")
			setChosenSearchInput("")
			setPatchRequest(true)
			setEditeDouble(false)
		} else {
			return
		}
	}

	//---------------- participants functions-------------------

	const onDragEnd = (result) => {
		if (!result.destination) {
			return
		}

		const updatedParticipants = Array.from(participants)
		const [removed] = updatedParticipants.splice(result.source.index, 1)
		updatedParticipants.splice(result.destination.index, 0, removed)

		dispatch(setParticipants(updatedParticipants))
	}

	const handleEditeDoubleParticipant = (participant) => {
		setPopupIsOpen(true)
		setFirstPlayerInDouble(`${participant?.player1?.last_name} ${participant?.player1?.first_name}`)
		setSecondPlayerInDouble(`${participant?.player2?.last_name} ${participant?.player2?.first_name}`)
		setUpdatingDoubleParticipant(participant)
		setEditeDouble(true)
	}

	const handleSaveListButton = () => {
		excludeParticipant()
		includeParticipant()
		updateDoubleParticipant()
	}

	const removeParticipantFromList = (participant) => {
		const updatedList = participants?.filter(p => participant?.uid !== p?.uid)
		dispatch(setParticipants(updatedList))
		setDeleteRequest(true)
	}

	const includeParticipant = async () => {
		if (!postRequest) {
			return
		}

		try {
			let body = null
			let response = {}

			if (props?.tournament?.type === "singles") {
				body = {
					"players": await formPostRequestForSingles(participants)
				}

				response = await addSinglePlayers({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()

			} else if (props?.tournament?.type === "doubles") {
				body = {
					"doubles": formPostRequestForDoubles(participants)
				}

				response = await addDouble({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}

			if (response?.error) {
				console.log("Include participant return error", response?.error)
				return
			}
			else {
				dispatch(setParticipants(response?.data))
				setShowButtonSaveList(false)
				setPostRequest(false)
				setSingleInputValue("")
				setFirstPlayerInDouble("")
				setSecondPlayerInDouble("")
				setDoubleParticipant([])
			}
		}
		catch (e) {
			console.log("Include participant catch error", e)
		}
	}

	const excludeParticipant = async () => {
		if (!deleteRequest) {
			return
		}

		try {
			let body = null
			let response = {}

			if (props?.tournament?.type === "singles") {
				body = {
					"players": formDeleteRequest(props?.tournament?.players, participants)
				}

				response = await removeParticipant({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}
			else if (props?.tournament?.type === "doubles") {
				body = {
					"doubles": formDeleteRequest(props?.tournament?.doubles, participants)
				}

				response = await removeDouble({
					tournament_uid: tournamentUid,
					body: body
				}).unwrap()
			}

			if (response?.error) {
				console.log("Exclude participant return error", response?.error)
				return
			}
			else {
				dispatch(setParticipants(response?.data))
				setDeleteRequest(false)
			}
		}
		catch (e) {
			console.log("Exclude participant catch error", e)
		}
	}

	const updateDoubleParticipant = async (participant) => {
		if (!patchRequest) {
			return
		}

		const body = {
			"doubles": formPatchRequest(participants)
		}

		try {
			const response = await updateDouble({
				tournament_uid: tournamentUid,
				body: body
			}).unwrap()

			if (response?.error?.status === 401) {
				navigate("/login-options")
			} else if (response?.error) {
				console.log("Updating double return error", response?.error)
				return
			} else {
				setParticipants(response?.data)
				setFirstPlayerInDouble("")
				setSecondPlayerInDouble("")
				setUpdatingDoubleParticipant({})
				setPatchRequest(false)
			}
		}
		catch (e) {
			console.log("Update double catch error", e)
		}
	}

	return (
		<>
			<div className="tournament__participants-section" style={{ marginBottom: showButtonSaveList ? "140px" : "80px" }}>
				<div className="tournament__participants-header">
					<p className="tournament__participants-title">{t("Number")} {`(${props?.tournament?.players?.length || props?.tournament?.doubles?.length || 0})`}</p>

					{popupIsOpen &&
						<Popup title={"Add participant"} onClose={() => closePopup(false)}>
							{popupIsOpen && props?.tournament?.type === "singles" &&
								<>
									<Input
										type="search"
										autoComplete="off"
										placeholder={t("Search player")}
										value={singlePlayerSearchValue}
										onChange={(e) => handleSinglePlayerInput(e.target.value)}
									/>
								</>
							}

							{props?.tournament?.type === "doubles" &&
								<>
									<div className="popup__input-player--first">
										<Input
											type="search"
											autoComplete="off"
											placeholder={t("Add player")}
											value={firstPlayerInDouble}
											onChange={(e) => handleFirstPlayerInDoubleInput(e.target.value)}
											onClick={() => handleInputOfDoublePopup(1)}
										/>
									</div>

									<div className="popup__input-player--second">
										<Input
											type="search"
											autoComplete="off"
											placeholder={t("Add player")}
											value={secondPlayerInDouble}
											onChange={(e) => handleSecondPlayerInDoubleInput(e.target.value)}
											onClick={() => handleInputOfDoublePopup(2)}
										/>
									</div>

									<div className="popup-button__addition-doubles">
										<Button title={t("Save double")} onClick={() => handleSaveDoubleParticipant()} />
									</div>
								</>
							}

							{
								searchPlayersList?.map((el, index) =>
									<div
										key={index}
										className="popup-search__list-item"
										style={{ backgroundColor: el?.activated ? "#EBF4FF" : "transparent" }}
									>
										<p className="popup-search__player-name" onClick={() => handlePlayerInPopupList(el)}>
											{el?.last_name} {el?.first_name} {el?.middle_name ? el?.middle_name : ""}
										</p>
									</div>
								)
							}
						</Popup>
					}
				</div>

				<div className="participants__button-addition">
					<ButtonWhite title={t("Add participant")} onClick={() => setPopupIsOpen(true)} />
				</div>

				<div className="participants">
					<DragDropContext onDragEnd={onDragEnd}>
						<Droppable droppableId="participants--singles">
							{
								(provided) => (
									<div className="participants--singles" {...provided.droppableProps} ref={provided.innerRef}>
										{
											participants?.map((el, index) =>
												<Draggable key={el?.uid} draggableId={el?.uid} index={index}>
													{
														(provided, snapshot) => (
															<div
																className="participants-item"
																ref={provided.innerRef}
																{...provided.draggableProps}
																{...provided.dragHandleProps}
																style={{
																	userSelect: "none",
																	backgroundColor: snapshot.isDragging ? "#F5FAFF" : "transparent",
																	...provided.draggableProps.style
																}}
															>
																{
																	props?.tournament?.type === "singles" ?
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">{index + 1}</p>
																			</div>

																			<p className="participants__name">{`${el?.last_name} ${el?.first_name}`}</p>

																			<div>
																				<ButtonDelete className="icon-deletion" onClick={() => removeParticipantFromList(el)} />
																			</div>
																		</>
																		:
																		<>
																			<div className="participants__raiting">
																				<p className="participants__number">{index + 1}</p>
																			</div>

																			<div className="participants__names">
																				<p className="participants__palyer--first">{el?.player1?.last_name} {el?.player1?.first_name}</p>

																				<p className="participants__palyer--second">{el?.player2?.last_name} {el?.player2?.first_name}</p>
																			</div>

																			<div className="participants-icons">
																				<Edit className="participants__icon-edit" onClick={() => handleEditeDoubleParticipant(el)} />
																				<ButtonDelete className="icon-deletion" onClick={() => removeParticipantFromList(el)} />
																			</div>
																		</>
																}
															</div>
														)
													}
												</Draggable>
											)
										}

										{provided.placeholder}
									</div>
								)
							}
						</Droppable>
					</DragDropContext>
				</div>

				<div className="participants__button-save">

					{showButtonSaveList && <Button onClick={handleSaveListButton} title={t("Save")} />}
				</div>
			</div>
		</>
	)
}

export default TournamentParticipants
