import "./Account.css"
import { useState, useEffect } from "react"
import { Link, useLocation, useNavigate } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { useGetCommunitiesQuery } from "../../redux/communitiesService/communitiesApiSlice"
import Header from "../../components/Header/Header.js"
import { ReactComponent as ArrowFollow } from "../../icon/arrow-follow.svg"
import { ReactComponent as EditBlue } from "../../icon/edit_blue.svg"
import BottomMenu from "../../components/BottomMenu/BottomMenu"

export default function Account() {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const location = useLocation()
  const authorized = localStorage.getItem("authorized")
  const communityUid = localStorage.getItem("communityUid")
  const { data = [], error, isLoading } = useGetCommunitiesQuery()
  const [communitites, setCommunities] = useState([])

  console.log(localStorage)

  useEffect(() => {
    if(!authorized){
      navigate("/login-options")
    }
  }, [authorized])

  useEffect(() => {
    if (Object.keys(data).length === 0) {
      return
    } 
    else {
      const formedCommunitites = data?.map(c => {
        return {
          nodeUid: c?.uid,
          checked: c?.uid === communityUid ? true : false,
          name: c?.name
        }
      })

      setCommunities(formedCommunitites)
    }
  }, [data])

  if (isLoading) { return <h1>Loading...</h1> }
  if (error) { console.log(error) }

  const handleChangeCommunity = (uid) => {
    const newCommunitiesList = communitites?.map(c => {
      if (uid === c?.nodeUid) {
        return { ...c, checked: true }
      } 
      else {
        return { ...c, checked: false }
      }
    })

    localStorage.setItem("communityUid", uid)
    setCommunities(newCommunitiesList)
  }

  console.log(localStorage)

  return (
    <>
      <Header
        title={t(" ")}
        settingsButton={true}
        location={location}
      />

      <div className="account__wrapper">
        <div className="account__block">
          <h2 className="account__block-title">
            {t("Communities")}
          </h2>

          <div className="account__communities-content">
            {
              communitites?.map((item, index) => (
                <div className="account__communities account__communities--border" key={index}>
                  <div className="account__communities-container">
                    {
                      data?.length > 1 ?
                        <div className="account__communities-container__radio">
                          <input
                            className="account__communities-container__radio-input"
                            id="radio-1"
                            type="radio"
                            name="radio"
                            checked={item?.checked}
                            onChange={() => handleChangeCommunity(item?.nodeUid)}
                          />

                          <label htmlFor="radio-1"></label>
                        </div>
                      : 
                        null
                    }

                    <p className="account__communities-item__name">
                      {t(item?.name)}
                    </p>

                    <EditBlue className="edit-icon" />
                  </div>
                </div>
              ))
            }
          </div>
        </div>

        <div className="account__block">
          <h2 className="account__block-title">
            {t("Menu")}
          </h2>

          <div className="account__menu">
            <Link to="/account/venues" className="account__menu-links account__menu-links--border">
              <p className="account__menu-links__item-subtitle">
                {t("Venues")}
              </p>

              <ArrowFollow className="account__menu-links__item-icon" />
            </Link>

            <Link to="/account/players" className="account__menu-links account__menu-links--border">
              <p className="account__menu-links__item-subtitle">
                {t("Players")}
              </p>

              <ArrowFollow className="account__menu-links__item-icon" />
            </Link>

            <Link to="/account/categories" className="account__menu-links links--border">
              <p className="account__menu-links__item-subtitle">
                {t("Tournament categories")}
              </p>

              <ArrowFollow className="account__menu-links__item-icon" />
            </Link>
          </div>
        </div>
      </div>

      <BottomMenu />
    </>
  )
}