import "./VenuesForm.css"
import { useTranslation } from "react-i18next"
import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { useAddVenueMutation, useUpdateVenueMutation } from "../../redux/venuesService/venuesApiSlice"
import useLocalStorage from "../../translator/hooks/useLocalStorage"
import { GeoapifyGeocoderAutocomplete, GeoapifyContext } from "@geoapify/react-geocoder-autocomplete"

import Header from "../../components/Header/Header"
import Input from "../../components/Input/Input"
import Button from "../../components/Button/Button"
import BottomMenu from "../../components/BottomMenu/BottomMenu"

const VenusForm = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const [language] = useLocalStorage("language", "ru")
  const updatedVenue = JSON.parse(sessionStorage.getItem("updatedVenue"))
  const communityUid = localStorage.getItem("communityUid")
  const [addVenue] = useAddVenueMutation()
  const [updateVenue] = useUpdateVenueMutation()

  const [name, setName] = useState(updatedVenue?.name ? updatedVenue?.name : "")
  const [address, setAddress] = useState(updatedVenue?.address_string ? updatedVenue?.address_string : "")
  const [surface, setSurface] = useState(updatedVenue?.surface ? updatedVenue?.surface : "other")
  const [type, setType] = useState(updatedVenue?.indoor_type ? updatedVenue?.indoor_type : "indoor")
  const [latitude, setLatitude] = useState(updatedVenue?.latitude ? updatedVenue?.latitude : "")
  const [longitude, setLongitude] = useState(updatedVenue?.longitude ? updatedVenue?.longitude : "")
  const [nameErr, setNameErr] = useState(false)
  const [addressErr, setAddressErr] = useState(false)
  const [addressOnfocus, setAddressOnfocus] = useState(false)
  const [err, setErr] = useState(false)

  function onPlaceSelect(value) {
    setAddress(value?.properties?.address_line2)
    setLatitude(value?.properties?.lat)
    setLongitude(value?.properties?.lon)
  }

  const handleNameAddress = () => {
    setAddressErr(false)
    setAddressOnfocus(true)
  }

  const handleName = () => {
    setNameErr(false)
    if (addressOnfocus) {
      setAddressOnfocus(false)
    }
  }

  const handleSearchStyle = () => {
    if (addressOnfocus) {
      setAddressOnfocus(false)
    }
  }

  const checkFields = () => {
    let fullFields = true

    if (!longitude || !latitude || !address) {
      setAddressErr(true)
      fullFields = false
    }

    if (!name) {
      setNameErr(true)
      fullFields = false
    }

    return fullFields
  }

  const handleSubmit = async (e) => {
    e.preventDefault()

    if (!checkFields()) {
      return
    }

    try {
      const body = {
        "uid": updatedVenue?.uid ? updatedVenue?.uid : "",
        "name": name,
        "latitude": Number(latitude).toFixed(8),
        "longitude": Number(longitude).toFixed(8),
        "address_string": address,
        "indoor_type": type,
        "surface": surface,
        "community_uids": [communityUid]
      }

      let response

      if (updatedVenue) {
        response = await updateVenue({ body, venue_uid: updatedVenue?.uid }).unwrap()
      } 
      else {
        response = await addVenue({ body }).unwrap()
      }

      if (response?.error?.status === 401) {
        navigate("/login-options")
      } else if (response?.error) {
        setErr(true)
        return
      } else {
        navigate("/account/venues")
      }
    } catch (e) {
      console.log("Venues creation and updating catch error", e)
      setErr(true)
    }
  }

  return (
    <>
      <Header
        title={updatedVenue ? t("Edition") : t("Add venue")}
        backButton={true}
      />

      <div className="venue-form__container">
        <form onSubmit={handleSubmit}>

          <div className="venue-form__input">
            <label className="venue-form__input-label">{t("Name")}</label>
            
            <Input
              value={name}
              onChange={(e) => setName(e.target.value)}
              onClick={handleName}
              type="text"
              borderColor={nameErr ? "#F20505" : ""}
              required={false}
            />

            {nameErr && <p className="venue-form__error">{t("Enter name")}</p>}
          </div>

          <div className="venue-form__search">
            <label className="venue-form__input-label">{t("Address")}</label>
            
            <div
              style={{
                border: addressErr ? "1px solid #F20505"
                  : addressOnfocus ? "1px solid #367BCF"
                    : "1px solid #CCC"
              }}
              className="venue-form__search-field"
              onClick={handleNameAddress}
            >
              <GeoapifyContext apiKey="fb6274f3d79d4a2babae8b9541e39de2">
                <GeoapifyGeocoderAutocomplete placeholder={t(" ")}
                  value={address}
                  lang={language}
                  placeSelect={onPlaceSelect}
                />
              </GeoapifyContext>
            </div>
            {addressErr && <p className="venue-form__error">{t("Enter address")}</p>}
          </div>

          <div className="venue-form__inputs-radio" onClick={handleSearchStyle}>
            <p className="inputs-radio__title">{t("Surface")}</p>

            <div className="inputs-radio__container">
              <input
                id="radio-clay"
                type="radio"
                name="surface"
                value="clay"
                onChange={() => setSurface("clay")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.surface === "clay" ? true : false}
              />
              <label htmlFor="radio-clay">
                {t("Clay")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-hard"
                type="radio"
                name="surface"
                value="hard"
                onChange={() => setSurface("hard")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.surface === "hard" ? true : false}
              />
              <label htmlFor="radio-hard">
                {t("Hard")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-wood"
                type="radio"
                name="surface"
                value="wood"
                onChange={() => setSurface("wood")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.surface === "wood" ? true : false}
              />
              <label htmlFor="radio-wood">
                {t("Wood")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-carpet"
                type="radio"
                name="surface"
                value="carpet"
                onChange={() => setSurface("carpet")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.surface === "wood" ? true : false}
              />
              <label htmlFor="radio-carpet">
                {t("Carpet")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-grass"
                type="radio"
                name="surface"
                value="grass"
                onChange={() => setSurface("grass")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.surface === "grass" ? true : false}
              />
              <label htmlFor="radio-grass">
                {t("Grass")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-other"
                type="radio"
                name="surface"
                value="null"
                defaultChecked={!updatedVenue?.surface ? true : false}
                onChange={() => setSurface("")}
                className="inputs-radio__button"
              />
              <label htmlFor="radio-other">
                {t("Other")}
              </label>
            </div>
          </div>

          <div className="venue-form__inputs-radio" onClick={handleSearchStyle}>
            {/* <p className="inputs-radio__title">{t("Court type")}</p> */}
            <div className="inputs-radio__separator"></div>

            <div className="inputs-radio__container">
              <input
                id="radio-indoor"
                type="radio"
                name="type"
                value="indoor"
                onChange={() => setType("indoor")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.indoor_type !== "outdoor" ? true : false}
              />
              <label htmlFor="radio-indoor">
                {t("Indoor")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="radio-outdoor"
                type="radio"
                name="type"
                value="outdoor"
                onChange={() => setType("outdoor")}
                className="inputs-radio__button"
                defaultChecked={updatedVenue?.indoor_type === "outdoor" ? true : false}
              />
              <label htmlFor="radio-outdoor">
                {t("Outdoor")}
              </label>
            </div>
          </div>

          {err && <p className="venue-form__main-error">{t("Something went wrong. Try later please")}</p>}

          <div className="venue-form__submit" onClick={handleSearchStyle}>
            <Button type={"submit"} title={t("Save")} />
          </div>
        </form>
      </div>

      <BottomMenu />
    </>
  )
}

export default VenusForm
