import "./OlympicPlus.css"
import React, { useEffect, useState, Fragment } from "react"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import {
	updateGroupName,
	removeLevel,
	removeGroup,
	removeParticipant,
	replaceParticipant,
	addParticipant,
	updateGroupNodes
} from "../../redux/stageService/stageSlice"
import { ReactComponent as Done } from "../../icon/done.svg"
import { ReactComponent as Edition } from "../../icon/edit_line_pen_blue.svg"
import { ReactComponent as Bucket } from "../../icon/bucket.svg"
import { ReactComponent as ArrowDown } from "../../icon/arrow-down.svg"
import Select from "../Select/Select"
import SmoothCollapse from "react-smooth-collapse"
import { Droppable, Draggable } from "react-beautiful-dnd"
import { ReactComponent as DND } from "../../icon/dnd.svg"
import { uniteNodesInPairs } from "./externalFunctions"
import { ReactComponent as ButtonDelete } from "../../icon/minus-delete.svg"
import useCtx from "../../redux/context/useCtx"
import { filterGroupsNodesInLevels, assignAlphabeticalNames } from "../../generalExternalFunctions"
import Popup from "../Popup/Popup"
import { addEmptyPlayerInList } from "./externalFunctions"
import { ReactComponent as StrucGreyNewIcon } from "../../icon/struc-grey-new.svg"

const selectOptions = [
	{
		"label": "Oplympic+",
		"value": 1
	},
	{
		"label": "Round Robin",
		"value": 2
	}
]

const OlympicPlus = (props) => {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { ctx, setCtx } = useCtx()
	// const stageLevels = useSelector(state => state?.stage?.stage?.levels)

	const [openedGroupNameEdition, setOpenedGroupNameEdition] = useState(false)
	const [groupName, setGroupName] = useState("")
	const [selectValue, setSelectValue] = useState({})
	const [hidenGroup, setHidenGroup] = useState(true)
	const [pairNodes, setPairNodes] = useState([])
	const [popupList, setPopupList] = useState([])
	const [popupIsOpened, setPopupIsOpened] = useState(false)
	const [replaceNameMode, setReplaceNameMode] = useState(false)
	const [replacedName, setReplacedName] = useState("")

	//   
	useEffect(() => {
		// set participants list according to nodes for group
		const updatedNodesList = addEmptyPlayerInList(props?.nodesList, props?.levels, props?.levelIndex)

		const updatedNodesListFilter = updatedNodesList.filter(node => {
			return node.firstPlayerUid !== null
		})

		if (props?.nodesList) {
			setPopupList(updatedNodesListFilter)
		}
	}, [props?.nodesList, props?.levels,  props?.levelIndex])

	//   ( )  input
	const handleInputEdition = (e) => {
		//    groupName
		setGroupName(e.target.value)
	}

	//       input
	const handleEditionDone = () => {
		//    
		dispatch(updateGroupName({
			levelIndex: props?.levelIndex,
			groupIndex: props?.groupIndex,
			groupName: groupName
		}))

		//  
		setOpenedGroupNameEdition(false)
	}

	//     
	const handleButtonEdition = (name) => {
		setOpenedGroupNameEdition(true)
	}

	//      
	const handleRemoveGroup = () => {
		if (props?.levels[props?.levelIndex]?.groups?.length === 1) {
			//          levelIndex
			dispatch(removeLevel(props?.levelIndex))
		} 
		else {
			//          levelIndex  groupIndex
			dispatch(removeGroup({
				levelIndex: props?.levelIndex,
				groupIndex: props?.groupIndex
			}))
		}
	}

	//  event      select
	const handleSelectChange = (event) => {
		setSelectValue(event)
	}

	//   ""  ""   ArrowDown
	const handleGroupArrow = () => {
		//    
		setHidenGroup(prev => !prev)
	}

	//    
	const excludeParticipant = (node) => {
		setCtx({ chosenPlayerInlist: "" })

		// exclude player from group and update redux statement
		const levelsWithFilteredGroups = filterGroupsNodesInLevels(props?.levels, node, true)

		dispatch(removeParticipant(levelsWithFilteredGroups))
		setCtx({ updatePlayersPlaces: true })
	}

	//   
	const openPopup = () => {
		setCtx({ chosenPlayerInlist: "" })

		const uniqueArray = filterPopupList(popupList, props?.group?.nodes)
		setPopupList(uniqueArray)
		setPopupIsOpened(true)
	}

	//     ,     
	const filterPopupList = (nodesList, groupNodes) => {
		// exclude players who are included in the group from popup list
		const uniqueArray = nodesList.filter(item1 => {
			const isDuplicate = groupNodes.some(item2 => item2?.nodeUid === item1?.nodeUid && item1?.orderNumber !== 0)
			return !isDuplicate
		})

		return uniqueArray
	}

	//         (   )
	const handleNameInPopup = (node, groupIndex, levelIndex) => {
		if (replaceNameMode) {
			replacePlayer(node, groupIndex, levelIndex)
		} 
		else {
			includeParticipant(node, groupIndex, levelIndex)
		}

		setReplaceNameMode(false)
	}

	//   
	const replacePlayer = (node, groupIndex, levelIndex) => {
		setCtx({ chosenPlayerInlist: "" })

		const levelsWithFilteredGroups = filterGroupsNodesInLevels(props?.levels, node, true)
		levelsWithFilteredGroups[levelIndex].groups[groupIndex].nodes[replacedName] = node
		dispatch(replaceParticipant(levelsWithFilteredGroups))
		setPopupIsOpened(false)

		setCtx({ updatePlayersPlaces: true })
	}

	//   
	const includeParticipant = (node, groupIndex, levelIndex) => {
		setCtx({ chosenPlayerInlist: "" })

		// include player in group and update redux statement
		const levelsWithFilteredGroups = filterGroupsNodesInLevels(props?.levels, node)
		const updatedNodes = [...levelsWithFilteredGroups[levelIndex]?.groups[groupIndex]?.nodes, node]
		const updatedGroup = { ...levelsWithFilteredGroups[levelIndex]?.groups[groupIndex], nodes: updatedNodes }
		levelsWithFilteredGroups[levelIndex].groups[groupIndex] = updatedGroup

		dispatch(addParticipant(levelsWithFilteredGroups))
		const filtredPopupList = popupList?.filter(el => {
			return el?.nodeUid !== node?.nodeUid
		})

		setPopupList(filtredPopupList)
		setCtx({ updatePlayersPlaces: true })
	}

	const handleName = (index, node, groupIndex, levelIndex) => {
		if (props?.editingMode && ctx?.chosenPlayerInlist) {
			putPlayerInGroup(index, node, groupIndex, levelIndex)
		} 
		else if (!props?.editingMode && ctx?.chosenPlayerInlist) {
			const levelsWithFilteredGroups = filterGroupsNodesInLevels(props?.levels, ctx?.chosenPlayerInlist, true)

			levelsWithFilteredGroups[props?.levelIndex].groups[props?.groupIndex].nodes[index] = ctx?.chosenPlayerInlist
			
			dispatch(replaceParticipant(levelsWithFilteredGroups))
			setCtx({ chosenPlayerInlist: "" })
		} 
		else {
			setReplacedName(index)
			setReplaceNameMode(true)
			openPopup()
		}

		setCtx({ updatePlayersPlaces: true })
	}

	const putPlayerInGroup = (index) => {
		const filtredLevels = filterGroupsNodesInLevels(props?.levels, ctx?.chosenPlayerInlist, false)

		dispatch(updateGroupNodes({
			levelIndex: props?.levelIndex,
			groupIndex: props?.groupIndex,
			levels: filtredLevels,
			index,
			addedNode: ctx?.chosenPlayerInlist
		}))

		setCtx({ chosenPlayerInlist: "" })
		setCtx({ updatePlayersPlaces: true })
	}

	//       
	useEffect(() => {
		const getPairNodes = uniteNodesInPairs(props?.levels?.[props?.levelIndex]?.groups?.[props?.groupIndex]?.nodes)

		setPairNodes(getPairNodes)
	}, [props?.levels, props?.levelIndex, props?.groupIndex])

	// console.log(props)
	
	return (
		<>
			<div className="olympic__header">
				<div className="olympic__name-container">
					{
						!props?.editingMode &&
							<StrucGreyNewIcon className="olympic__type-icon" />
					}

					<p className="olympic__name">{t("Group")} {!openedGroupNameEdition ? props?.group?.name : ""}</p>

					{ 
						openedGroupNameEdition &&
							<div className="olympic__name-edition">
								<input
									type="text"
									name="groupName"
									className="olympic__input-edition"
									value={ groupName }
									onChange={ e => handleInputEdition(e) }
								/>

								<div onClick={() => handleEditionDone()} className="olympic__icon-done">
									<Done />
								</div>
							</div>
					}

					{
						!props?.editingMode && !openedGroupNameEdition && 
							<div className="olympic__icon-edition">
								<Edition className="edition-icon" onClick={() => handleButtonEdition(props?.group?.name)} />
							</div>
					}
				</div>

				{
					props?.editingMode &&
						<div className="olympic__icon-deletion">
							<Bucket onClick={() => handleRemoveGroup()} />
						</div>
				}
			</div>

			{
				props?.editingMode &&
					<div className="olympic__select">
						<Select
							options={ selectOptions }
							value={ selectValue }
							defaultValue={ selectValue }
							onChange={ e => handleSelectChange(e) }
						/>
					</div>
			}

			<div className="olympic__players-header" onClick={ handleGroupArrow }>
				<p className="olympic__players-subtitle">
					{t("Players")} { pairNodes?.length * 2 }
				</p>

				<ArrowDown className={ hidenGroup ? "arrow--up" : "arrow--down" } />
			</div>

			{
				props?.editingMode && hidenGroup &&
					<div>
						<p onClick={openPopup} className="olympic__participants-addition">{t("Add player")}</p>
					</div>
			}

			{
				popupIsOpened &&
					<Popup
						onClose={() => setPopupIsOpened(false)}
						title={t("Participants")}
					>
						{
							popupList?.map((node, index) =>
								<div
									key={index} className={`olympic__popup-participants-item ${node?.placed ? "olympic__popup-participants-item--selected" : ""}`}
									onClick={() => handleNameInPopup(node, props?.groupIndex, props?.levelIndex)}
								>
									{
										props?.tournament?.type === "singles" ?
											<>
												<p className="olympic__popup-participants-name">
													({node?.rating ? node?.rating : "-"}) {node?.name ? node?.name : "---"}
												</p>
									
												{/* <p className="olympic__popup-participants-name">
													{ node?.groupName }
												</p> */}
											</>
										:
											<div className="olympic__popup-participants-doubles">
												<p className="olympic__popup-doubles-rating">({node?.rating ? node?.rating : "-"})</p>
													
												{
													node?.name ?
														<p>{node?.name}</p>
													:
														<div>
															<p className="olympic__popup-participants-name--first">{node?.firstPlayerName ? node?.firstPlayerName : "---"}</p>
															<p className="olympic__popup-participants-name--second">{node?.secondPlayerName ? node?.secondPlayerName : "---"}</p>
														</div>
												}
											</div>
									}

									<p>{node?.groupName}</p>
								</div>
							)
						}
					</Popup>
			}

			<SmoothCollapse expanded={hidenGroup}>
			<Droppable key={props?.groupIndex} droppableId={`${props?.levelIndex}-${props?.groupIndex}`}>
				{
						(provided) => (
							<div {...provided.droppableProps} ref={provided.innerRef}>
								{
									pairNodes.map((node, nodeIndex) => {
										const nodeOneIndex = nodeIndex * 2
    								const nodeTwoIndex = nodeIndex * 2 + 1

										return <div className="olympic__players" key={nodeIndex}>

											<p className="olympic__players-number">{ nodeIndex + 1 }</p>

											<div className="olympic__players-container">
												<Draggable
													key={node?.nodeOne?.nodeUid}
													draggableId={node?.nodeOne?.nodeUid}
													index={nodeOneIndex}
													isDragDisabled={props?.editingMode ? true : false}
												>
													{
														(provided, snapshot) => (
															<div
																className={`olympic__players-item ${props?.editingMode ? "" : (node?.nodeOne?.bye || node?.nodeOne?.disabled) ? "olympic__players-item--grey" : ""}`}
																ref={provided.innerRef}
																{...provided.draggableProps}
																style={{
																	userSelect: "none",
																	backgroundColor: snapshot.isDragging ? "#F5FAFF" : "",
																	zIndex: 0,
																	...provided.draggableProps.style
																}}
															>

																{
																	props?.tournament?.type === "singles" ?
																		<p className="olympic__players-name" onClick={() => handleName(node?.nodeOne?.orderNumber - 1, node, props?.groupIndex, props?.levelIndex)}>
																			({node?.nodeOne?.rating ? node?.nodeOne?.rating : "-"}) {node?.nodeOne?.name ? node?.nodeOne?.name : "---"}
																		</p>
																	:
																		<div className="olympic__name-content">
																			<p className="olympic__players-rating">({node?.nodeOne?.rating ? node?.nodeOne?.rating : "-"})</p>

																			<div className="olympic__name-dubble-content" onClick={() => handleName(node?.nodeOne?.orderNumber - 1, node, props?.groupIndex, props?.levelIndex)}>
																				<p className="olympic__players-name">
																					{
																						node?.nodeOne?.firstPlayerName ? node?.nodeOne?.firstPlayerName : "---"
																					}
																				</p>

																				<p className="olympic__players-name">
																					{
																						node?.nodeOne?.secondPlayerName ? node?.nodeOne?.secondPlayerName : "---"
																					}
																				</p>
																			</div>
																		</div>
																}

																{
																	props?.editingMode ?
																		<>
																			<ButtonDelete
																				className="icon-deletion"
																				onClick={() => excludeParticipant(node?.nodeTwo)}
																			/>
																		</>
																	:
																		<div {...provided.dragHandleProps} className={`roundrobin__dnd ${!props?.editingMode && node?.nodeOne?.bye && node?.nodeOne?.disabled ? "roundrobin__dnd--disable " : ""}`}>
																			<DND />
																		</div>
																}
															</div>
														)
													}
												</Draggable>
									
												<Draggable
													key={node?.nodeTwo?.nodeUid}
													draggableId={node?.nodeTwo?.nodeUid}
													index={nodeTwoIndex}
													isDragDisabled={props?.editingMode ? true : false}
												>
													{
														(provided, snapshot) => (
															<div
																// className={`olympic__players-item ${props?.editingMode ? "" : (!node?.nodeTwo?.firstPlayerName || !node?.nodeTwo?.secondPlayerName) && (!node?.nodeTwo?.name) ? "olympic__players-item--grey" : ""}`}

																className={`olympic__players-item ${props?.editingMode ? "" : (node?.nodeTwo?.bye || node?.nodeTwo?.disabled) ? "olympic__players-item--grey" : ""}`}
																ref={provided.innerRef}
																{...provided.draggableProps}
																style={{
																userSelect: "none",
																	backgroundColor: snapshot.isDragging ? "#F5FAFF" : "",
																	...provided.draggableProps.style
																}}
															>
																
																{
																	props?.tournament?.type === "singles" ?
																		<p className="olympic__players-name" onClick={() => handleName(node?.nodeTwo?.orderNumber - 1, node, props?.groupIndex, props?.levelIndex)}>({node?.nodeTwo?.rating ? node?.nodeTwo?.rating : "-"}) {node?.nodeTwo?.name ? node?.nodeTwo?.name : "---"}</p>
																	:
																		<div className="olympic__name-content">
																			<p className="olympic__players-rating">({node?.nodeTwo?.rating ? node?.nodeTwo?.rating : "-"})</p>

																			<div className="olympic__name-dubble-content" onClick={() => handleName(node?.nodeTwo?.orderNumber - 1, node, props?.groupIndex, props?.levelIndex)}>
																				<p className="olympic__players-name">{node?.nodeTwo?.firstPlayerName ? node?.nodeTwo?.firstPlayerName : "---"}</p>
																				<p className="olympic__players-name">{node?.nodeTwo?.secondPlayerName ? node?.nodeTwo?.secondPlayerName : "---"}</p>
																			</div>
																		</div>
																}

																{
																	props?.editingMode ?
																		<>
																			<ButtonDelete
																				className="icon-deletion"
																				onClick={() => excludeParticipant(node?.nodeTwo)}
																			/>
																		</>
																	:
																		<div {...provided.dragHandleProps} className={`roundrobin__dnd ${!props?.editingMode && node?.nodeTwo?.bye && node?.nodeTwo?.disabled ? "roundrobin__dnd--disable" : ""}`}>
																			<DND />
																		</div>
																}
															</div>
														)
													}
												</Draggable>
											</div>
										</div>
									})
								}

								{ provided.placeholder }
							</div>
						)
					}
  			</Droppable>
			</SmoothCollapse>
		</>
	)
}

export default OlympicPlus




























































