import "./Tournaments.css"
import { useState, useEffect } from "react"
import { useGetCommunityQuery } from "../../redux/communityService/communityApiSlice"
import { useTranslation } from "react-i18next"
import { useNavigate } from "react-router-dom"
import dayjs from "dayjs"
import customParseFormat from "dayjs/plugin/customParseFormat"
import { ReactComponent as CalendarIcon } from "../../icon/calendar.svg"
import Header from "../../components/Header/Header"
import Popup from "../../components/Popup/Popup"
import Calendar from "../../components/Calendar/Calendar"
import TournamentsList from "../../components/TournamentsList/TournamentsList"
import { ReactComponent as TournamentAddition } from "../../icon/add_tournaments.svg"
import BottomMenu from "../../components/BottomMenu/BottomMenu"
import PopupError from "../../components/PopupError/PopupError"

const Tournaments = () => {
  sessionStorage.clear()
  dayjs.extend(customParseFormat)
  const { t } = useTranslation()
  const navigate = useNavigate()
  const communityUid = localStorage.getItem("communityUid")
  const { data = {}, isLoading, error } = useGetCommunityQuery({ community_uid: communityUid })

  const [isOpen, setIsOpen] = useState(false)
  const [tournaments, setTournaments] = useState([])
  const [filterPlaceholder, setFilterPlaceholder] = useState(t("Filter by date"))
  const [startDate, setStartDate] = useState(new Date())
  const [endDate, setEndDate] = useState(new Date())

  useEffect(() => {
    if (Object.keys(data).length === 0) {
      return
    }
    else {
      setTournaments(data?.tournaments)
    }
  }, [data])

  const resetDate = () => {
    setStartDate(new Date())
    setEndDate(new Date())
    setTournaments(data?.tournaments)
    setFilterPlaceholder("Filter by date")

    handleClose()
  }

  const handleOpen = () => {
    setIsOpen(true)
  }
  const handleClose = () => {
    setIsOpen(false)
  }

  const dateFilter = () => {
    const stDate = new Date(startDate)
    const stFormatDate = `${stDate.getDate()}.${stDate.getMonth() + 1}.${stDate.getFullYear()}`

    const ndDate = new Date(endDate)
    const ndFormatDate = `${ndDate.getDate()}.${ndDate.getMonth() + 1}.${ndDate.getFullYear()}`

    const filtredTournaments = data?.tournaments?.filter(value =>
      dayjs(value?.start_date).isAfter(dayjs(startDate)) && dayjs(value?.start_date).isBefore(dayjs(endDate))
    )

    setTournaments(filtredTournaments)
    setFilterPlaceholder(`${stFormatDate} - ${ndFormatDate}`)
    handleClose()
  }

  if (isLoading) { return <h1>Loading...</h1> }
  if (error) { console.log(error) }

  const goToCreateTournament = () => {
    navigate(`/communities/${communityUid}/tournaments/form`)
  }

  console.log(localStorage)

  return (
    <>
      <Header
        className="header-container"
        backButton={false}
        title={"Community"}
        subtitle={t("Tournaments")}
      />

      <div className="tournaments-wrapper">
        <div className="tournaments-container">
          {/* <PopupError /> */}
          {
            data?.result?.length < 1 ?
              <div className="no-tournaments__container">
                <p className="no-tournaments__text">{t("Your tournaments will be presented here")}</p>
              </div>
              :
              <>
                <div className="tournaments-calendar__container" onClick={handleOpen}>
                  <p className="tournaments-calendar__title">
                    {t(filterPlaceholder)}
                  </p>

                  <CalendarIcon className="tournaments-calendar__icon" />
                </div>

                {
                  isOpen &&
                  <Popup title={t("Filter by date")} onClose={handleClose}>
                    <Calendar
                      selected={startDate}
                      onChange={(date) => setStartDate(date)}
                      selectsStart
                      startDate={startDate}
                      endDate={endDate}
                    />

                    <Calendar
                      selected={endDate}
                      onChange={(date) => setEndDate(date)}
                      selectsEnd
                      endDate={endDate}
                      minDate={startDate}
                    />

                    <div className="filter__button-container">
                      <button className="filter__button" onClick={resetDate}>
                        {t("Reset")}
                      </button>

                      <button className="filter__button" onClick={dateFilter}>
                        {t("Show")}
                      </button>
                    </div>
                  </Popup>
                }

                <TournamentsList list={tournaments} data={data} />

                <div className="tournaments__button-addition">
                  <TournamentAddition onClick={goToCreateTournament} />
                </div>
              </>
          }
        </div>
      </div>

      <BottomMenu />
    </>
  )
}

export default Tournaments