import "./TournamentSchedule.css"
import React, {useEffect, useState} from "react"
import { useParams } from "react-router-dom"
import { useGetStageQuery } from "../../redux/stageService/stageApiSlice"
import { useSelector } from "react-redux"
import { assignAlphabeticalNames } from "../../generalExternalFunctions"
import TableMatches from "../../components/TableMatches/TableMatches.js"

const TournamentSchedule = (props) => {
  const { communityUid, tournamentUid } = useParams()
  const { data = {}, error, isLoading } = useGetStageQuery({
    community_uid: communityUid,
    tournament_uid: tournamentUid,
    stageNumber: props?.stageNumber
  }) // Stage data
  const tournamentState = useSelector(state => state.tournament.tournament) //   

  const [stage, setStage] = useState({})

  useEffect(() => {
    const nameGroupsInStage = async () => {
      if (Object.keys(data) !== 0) {
        const stageWithNamedGroups = await assignAlphabeticalNames(data)
        setStage(stageWithNamedGroups)
      }
    }

    nameGroupsInStage()
  }, [data])

  if (error) { console.log(error) }
  if (isLoading) { return <h1>Loading</h1> }

  return (
    <div className="schedule__container">
      {
        <TableMatches
          data={stage}
          tournamentState={tournamentState}
        />
      }
    </div>
  )
}

export default TournamentSchedule