import "./OlimpicPlusGrid.css"
import React, { useState, useEffect } from "react"
import { useTranslation } from "react-i18next"
import useCtx from "../../redux/context/useCtx"
import { formGrid } from "./externalFunctions"
import { useSelector } from "react-redux"

const OlimpicPlusGrid = (props) => {
  const { t } = useTranslation()
  const { ctx, setCtx } = useCtx()
  const tournamentState = useSelector(state => state.tournament.tournament)
  const [grid, setGrid] = useState([])
  const [columnOfResults, setColumnOfResults] = useState([])

  useEffect(() => {
    if (Object.keys(props?.group) !== 0) {
      const readyGrid = formGrid(props?.group, tournamentState)

      if (readyGrid) {
        setGrid(readyGrid?.activeColumns)
        setColumnOfResults(readyGrid?.resultsColumn)
      }
    }
  }, [props?.group])

  const setStyles = (columnIndex, matches, match, matchIndex) => {
    if (columnIndex < 1) {
      return
    } 
    else if (match?.highlight && match?.borderTop && match?.borderBottom) {
      return {
        backgroundColor: "#EBF4FF",
        borderTop: "1px solid #367BCF",
        borderBottom: "1px solid #367BCF"
      }
    } 
    else if (match?.highlight && match?.borderTop) {
      return {
        backgroundColor: "#EBF4FF",
        borderTop: "1px solid #367BCF"
      }
    } 
    else if (match?.highlight && match?.borderBottom) {
      return {
        backgroundColor: "#EBF4FF",
        borderBottom: "1px solid #367BCF"
      }
    } 
    else if (match?.highlight) {
      return {
        backgroundColor: "#EBF4FF",
      }
    } 
    else if (!match?.highlight && matches[matchIndex + 1].highlight) {
      return {
        borderBottom: "none"
      }
    }
  }

  const goToMatch = (uid) => {
    if (!uid) {
      return
    } 
    else {
      setCtx({ ...ctx, match: true, matchUid: uid })
      sessionStorage.setItem("match", true)
      sessionStorage.setItem("matchUid", uid)
    }
  }

  return (
    <div className="olympicplus-grid__container">
      <p className="olympicplus-grid__title">{t("Group")} {props?.group?.name}</p>

      <div className="olympicplus-grid__columns">
        {
          grid?.map((column, columnIndex) =>
            <div className="olympicplus-grid__column" key={columnIndex}>
              <div className="olympicplus-grid__column-title">
                {
                  columnIndex === 0 ?
                    <div className="olympicplus-grid__column-title--first">
                      <span className="olympicplus-grid__column-title-symbole"></span>
                      <span className="olympicplus-grid__column-title-round--first">{t(column?.roundName)}</span>
                    </div>
                  : 
                    <span className="olympicplus-grid__column-title-round">{t(column?.roundName)}</span>
                }
              </div>

              <div className={columnIndex === 0 ? "olympicplus-grid__column-container--first" : "olympicplus-grid__column-container"}>
                {
                  column?.matches?.map((match, matchIndex) =>
                    <div
                      onClick={() => goToMatch(match?.matchUid)}
                      className={match?.nodeOne?.type === "incoming" ? "olympicplus-grid__cell--first" : "olympicplus-grid__cell"}
                      key={matchIndex}
                      style={setStyles(columnIndex, column?.matches, match, matchIndex)}
                    >
                      <p className="olympicplus-grid__cell-number">{match?.match ? match?.matchNumber : ""}</p>
                  
                      <div
                        className={match?.nodeOne?.type === "incoming" ?
                          "olympicplus-grid__cell-container--first"
                        : 
                          "olympicplus-grid__cell-container"}
                      >
                        {
                          props?.tournamentState?.type === "singles" &&
                            <div className="olympicplus-grid__cell-players">
                              <p className="olympicplus-grid__cell-name">{match?.nodeOne?.name}</p>
                              <p className="olympicplus-grid__cell-name">{match?.nodeTwo?.name}</p>
                            </div>
                        }

                        {
                          props?.tournamentState?.type === "doubles" &&
                            <div className="olympicplus-grid__cell-doubles">
                              {
                                !match?.nodeOne?.name ?
                                  <div className="olympicplus-grid__cell-doubles-item">
                                    <p className="olympicplus-grid__cell-name--doubles">{match?.nodeOne?.firstPlayerName}</p>
                                    <p className="olympicplus-grid__cell-name--doubles">{match?.nodeOne?.secondPlayerName}</p>
                                  </div>
                                : 
                                  <p className="olympicplus-grid__cell-noname">{match?.nodeOne?.name}</p>
                              }
                        
                              {
                                !match?.nodeTwo?.name ?
                                  <div className="olympicplus-grid__cell-doubles-item">
                                    <p className="olympicplus-grid__cell-name--doubles">{match?.nodeTwo?.firstPlayerName}</p>
                                    <p className="olympicplus-grid__cell-name--doubles">{match?.nodeTwo?.secondPlayerName}</p>
                                  </div>
                                : 
                                  <p className="olympicplus-grid__cell-noname">{match?.nodeTwo?.name}</p>
                              }
                            </div>
                        }

                        {
                          match?.sets?.length > 0 &&
                            <div className="olympicplus-grid__cell-result">
                              {
                                match?.sets?.map((s, setIndex) =>
                                  <div className="olympicplus-grid__cell-scores" key={setIndex}>
                                    <p className="olympicplus-grid__cell-score">
                                      {s?.score1}
                              
                                      <sup className="olympicplus-grid__cell-tiebreak">{s?.tie_break_score1 ? s?.tie_break_score1 : ""}</sup>
                                    </p>
                            
                                    <p className="olympicplus-grid__cell-score">
                                      {s?.score2}
                                      
                                      <sup className="olympicplus-grid__cell-tiebreak">{s?.tie_break_score2 ? s?.tie_break_score2: ""}</sup>
                                    </p>
                                  </div>
                                )
                              }
                            </div>
                        }
                      </div>
                    </div>
                  )
                }
              </div>
            </div>
          )
        }

        <div className="olympicplus-grid__column">
          <p className="olympicplus-grid__column-title">{t("Result")}</p>
          
          <div className="olympicplus-grid__result">
            {
              columnOfResults?.map((cell, cellIndex) =>
                <div
                  key={cellIndex}
                  className="olympicplus-grid__result-container"
                  style={{height: tournamentState?.type === "singles" ? "4.5vh" : "6.5vh"}}
                >
                  {
                    <p className="olympicplus-grid__result-place">
                      {cell?.name !== "---" ? cellIndex + 1 : ""}
                    </p>
                  }
                  
                  <p className="olympicplus-grid__result-name">
                    {cell?.name}
                  </p>
                </div>
              )
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default OlimpicPlusGrid