import "./Login.css"
import { useState, useEffect } from "react"
import { Link, useNavigate } from "react-router-dom"
import { useLoginMutation } from "../../redux/authService/authApiSlice"
import { ReactComponent as Eye } from "../../icon/hide-password.svg"
import { useTranslation } from "react-i18next"
import fieldsChecker from "form-fields-checker"
import Input from "../../components/Input/Input"
import Button from "../../components/Button/Button"
import Header from "../../components/Header/Header.js"

export default function Login() {
	const { t } = useTranslation()
	const loginType = sessionStorage.getItem("loginType")
	const [logIn] = useLoginMutation()
	const navigate = useNavigate()

	const [password, setPassword] = useState("")
	const [login, setLogin] = useState("")
	const [err, setErr] = useState("")
	const [errLogin, setErrLogin] = useState("")
	const [errPassword, setErrPassword] = useState("")
	const [passwordShown, setPasswordShown] = useState(false)
	const [loginName, setLoginName] = useState("Login")

	useEffect(() => {
		if (loginType === "email") {
			setLoginName("Email")
		} else if (loginType === "tel") {
			setLoginName("Phone number")
		}
	}, [loginType])

	const handleLoginInput = (value) => {
		if (value.startsWith("8")) {
			setLogin("+7" + login?.slice(1))
		} else if (value.startsWith("9")) {
			setLogin("+7" + value)
		} else if (value.startsWith("7")) {
			setLogin(prev => "+7" + prev)
		} else {
			setLogin(value)
		}
	}

	const checkFields = () => {
		if (!login) {
			setErrLogin(t("Enter login"))
		}

		if (!password) {
			setErrPassword(t("Enter password"))
		}

		if (!login || !password) {
			return false
		} else {
			return true
		}
	}


	const validateLogin = async () => {
		if (loginType === "email") {

			if (fieldsChecker?.isValidEmail(login) !== true) {
				setErrLogin(t("Invalid email"))
				return false
			} else {
				return true
			}
		} else if (loginType === "tel") {

			if (fieldsChecker?.isValidPhone(login, ["11"]) !== true) {
				setErrLogin(t("Invalid phone number"))
				return false
			} else {
				return true
			}
		} else {
			return true
		}
	}

	const handleSubmit = async (event) => {
		event.preventDefault()
		if (errLogin || errPassword) {
			return
		}

		if (!checkFields()) {
			return
		}

		const isValidate = await validateLogin()
		if (!isValidate) {
			return
		}

		try {
			const response = await loginRequest(login, password)

			localStorage.setItem("refreshToken", response?.refresh)
			localStorage.setItem("accessToken", response?.access)
			localStorage.setItem("authorized", true)

			setLogin("")
			setPassword("")
			navigate("/communities")
		} catch (e) {
			if (e?.status === 500) {
				setErr(t("Something went wrong. Try later please"))
			} else if (e) {
				setErrPassword(t("Incorrect password"))
				setErrLogin(
					loginType === "email" ? t("Incorrect email") :
						loginType === "tel" ? t("Incorrect phone number") :
							t("Incorrect login")
				)
			}

			console.log("Login catch error", e)
			localStorage.clear()
		}
	}

	const loginRequest = (login, password) => {
		let body = null

		if (loginType === "tel") {
			body = { mobile: login, password }
		} else if (loginType === "email") {
			body = { email: login, password }
		} else {
			body = { username: login, password }
		}

		return logIn(body).unwrap()
	}

	const handlePassword = (e) => { setPassword(e.target.value) }
	const showPassword = () => { setPasswordShown(!passwordShown) }

	return (
		<>
			<Header
				className="header-container"
				title={t("Login")}
				backButton={true}
			/>

			<div className="login__container">
				<form onSubmit={handleSubmit} className="login__form">
					<div className="login__form-input">
						<label className="login__input-label" htmlFor="login">{t(loginName)}</label>

						<div className="login__input-login">
							<Input
								value={login}
								onChange={e => handleLoginInput(e.target.value)}
								onClick={() => setErrLogin("")}
								type={loginType ? loginType : "text"}
								id="login"
								borderColor={errLogin ? "#F20505" : ""}
								required={false}
							/>
						</div>

						{errLogin && <p className="login__error-login">{errLogin}</p>}
					</div>

					<div className="login__form-input" style={{ position: "relative" }}>
						<label className="login__input-label" htmlFor="password">{t("Password")}</label>

						<div className="login__input-password">
							<Input
								value={password}
								onChange={handlePassword}
								onClick={() => setErrPassword("")}
								type={passwordShown ? "text" : "password"}
								id="password"
								borderColor={errPassword ? "#F20505" : ""}
								required={false}
							/>
						</div>

						<Eye onClick={showPassword} className="login__input-icon" />
					</div>

					{errPassword && <p className="login__error-password">{errPassword}</p>}

					{err && <p className="login__form-error">{err}</p>}

					<div className="login_button-submit">
						<Button type={"submit"} title={t("Sing in")} />
					</div>
				</form>

				<Link to="#" className="login__password-restoration">{t("Forgot your password?")}</Link>
			</div>
		</>
	)
}