import "./TournamentStanding.css"
import React, { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { useGetStageQuery } from "../../redux/stageService/stageApiSlice"
import { useSelector } from "react-redux"
import { assignAlphabeticalNames } from "../../generalExternalFunctions"
import RoundRobinGrid from "../../components/RoundRobinGrid/RoundRobinGrid"
import OlympicPlusGrid from "../../components/OlympicPlusGrid/OlimpicPlusGrid"

const TournamentStanding = (props) => {
  const { t } = useTranslation()
  const { communityUid, tournamentUid } = useParams()
  const { data = {}, error, isLoading } = useGetStageQuery({
    community_uid: communityUid,
    tournament_uid: tournamentUid,
    stageNumber: props?.stageNumber
  }) // Stage data
  const tournamentState = useSelector(state => state.tournament.tournament) //   

  const [stage, setStage] = useState({})

  useEffect(() => {
    const nameGroupsInStage = async () => {
      if (Object.keys(data) !== 0) {
        const stageWithNamedGroups = await assignAlphabeticalNames(data)
        setStage(stageWithNamedGroups)
      }
    }

    nameGroupsInStage()
  }, [data])

  if (isLoading) { return <h1>Loading</h1> }
  if (error) { console.log(error) }

  return (
    <>
      <div className="standing-container">
        <div className="standing-wrapper">
          {
            stage?.levels?.map((level, levelIndex) => {
              return (
                <React.Fragment key={levelIndex}>
                  {
                    stage?.levels?.length > 1 &&
                      <p className="standing-header">
                        {t("Level") + " " + (levelIndex + 1)}
                      </p>
                  }

                  {
                    level?.groups?.map((grp, index) =>
                      <div key={index}>
                        {
                          grp?.type === "round_robin" ?
                            <RoundRobinGrid
                              group={grp}
                              groupIndex={index}
                              tournamentState={tournamentState}
                            />
                          :
                            <OlympicPlusGrid
                              group={grp}
                              groupIndex={index}
                              tournamentState={tournamentState}
                            />
                        }
                      </div>
                    )
                  }
                </React.Fragment>
              )
            })
          }
        </div>
      </div>
    </>
  )
}

export default TournamentStanding