import "./StageForm.css"
import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { useFormStageMutation } from "../../redux/stageService/stageApiSlice"
import { useDispatch, useSelector } from "react-redux"
import { addLevel, addGroup, removeLevel, setStageLevels } from "../../redux/stageService/stageSlice"
import { useRemoveStageMutation } from "../../redux/singleTournamentService/singleTournamentApiSlice"
import { dragParticipant } from "../../redux/stageService/stageSlice"
import {
	dragItems,
	formLevelsAndNodes,
	generateGroupName,
	definePlacedPlayers
} from "./externalFunctions"
import { DragDropContext } from "react-beautiful-dnd"
import { useGetStageQuery } from "../../redux/stageService/stageApiSlice"
import useCtx from "../../redux/context/useCtx"
import { assignAlphabeticalNames, checkEquality } from "../../generalExternalFunctions"
import Button from "../../components/Button/Button"
import RoundRobin from "../../components/RoundRobin/RoundRobin"
import OlympicPlus from "../../components/OlympicPlus/OlympicPlus"
import ButtonStructure from "../../components/ButtonWhite/ButtonWhite"
import SmoothCollapse from "react-smooth-collapse" // animation for hidding elements
import { ReactComponent as ArrowDown } from "../../icon/arrow-down.svg"
import { ReactComponent as Bucket } from "../../icon/bucket.svg"
import { ReactComponent as EditBlueIcon } from "../../icon/build_circle_blue.svg"
import { ReactComponent as EditPenBlueIcon } from "../../icon/edit_pen_blue.svg"
import { ReactComponent as CloseIcon } from "../../icon/close.svg"

const StageForm = (props) => {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { ctx, setCtx } = useCtx()
	const { communityUid, tournamentUid } = useParams()
	const { data = {}, error, isLoading } = useGetStageQuery({
		community_uid: communityUid,
		tournament_uid: tournamentUid,
		stageNumber: props?.stageNumber
	})
	const stageLevels = useSelector(state => state.stage.stage.levels)
	const tournamentState = useSelector(state => state.tournament.tournament)
	const [formStage] = useFormStageMutation()
	const [removeStage] = useRemoveStageMutation()
	const immutableLevelsState = sessionStorage.getItem("immutableLevelsState")

	const [editingMode, setEditingMode] = useState(false)
	const [nodesList, setNodesList] = useState([])
	const [showParticipantsList, setShowParticipantsList] = useState(false)
	const [showSaveButton, setShowSaveButton] = useState(false)

	useEffect(() => {
		if (Object.keys(data).length === 0) {
			return
		}
		else {
			const levelsWithNamedGroup = assignAlphabeticalNames(data)

			let formedLevelsAndNodes = null
			if (props?.tournament?.type === "singles") {
				formedLevelsAndNodes = formLevelsAndNodes(
					levelsWithNamedGroup?.levels,
					tournamentState?.players,
					tournamentState?.type,
					props?.stageNumber
				)
			}
			else if (props?.tournament?.type === "doubles") {
				formedLevelsAndNodes = formLevelsAndNodes(
					levelsWithNamedGroup?.levels,
					tournamentState?.doubles,
					tournamentState?.type,
					props?.stageNumber
				)
			}

			const nodesListFilter = formedLevelsAndNodes?.nodes.filter(node => {
				// return node.firstPlayerUid !== null

				return node.bye !== true || node.disabled !== true
			})

			setNodesList(nodesListFilter)
			dispatch(setStageLevels(formedLevelsAndNodes?.levelsWithformedGroupsNodes))
			sessionStorage.setItem("immutableLevelsState", JSON.stringify(formedLevelsAndNodes?.levelsWithformedGroupsNodes))
		}
	}, [data, props?.stageNumber, tournamentState])

	useEffect(() => {
		if (ctx?.updatePlayersPlaces) {
			const placedPlayers = definePlacedPlayers(stageLevels, nodesList)
			setNodesList(placedPlayers)

			setCtx(prevCtx => {
				return { ...prevCtx, updatePlayersPlaces: false }
			})
		}
	}, [stageLevels])

	useEffect(() => {
		const isEquale = checkEquality(JSON.parse(immutableLevelsState), stageLevels)

		if (isEquale) {
			setShowSaveButton(false)
		} else {
			setShowSaveButton(true)
		}
	}, [stageLevels])

	if (error) { console.log(error) }
	if (isLoading) { return <h1>Loading</h1> }

	const onDragEnd = (result) => {
		if (!result.destination) {
			return
		}

		const endLevelIndex = result.destination.droppableId.split("-")[0]
		const finishGroupIndex = result.destination.droppableId.split("-")[1]
		const replacedNode = {...stageLevels[endLevelIndex]?.groups[finishGroupIndex]?.nodes[ result.destination.index]}

		if(replacedNode.bye && replacedNode.disabled){
			return
		}

		const obj = dragItems(result, stageLevels)
		dispatch(dragParticipant({ obj }))
	}

	const handleSaveFormedStage = () => {
		try {
			console.log({ ...stageLevels })
			// const response = formStage().unwrap()

			// if (response?.error?.status === 401) {
			// 	navigate("/login-option")
			// }
			// if (response?.error) {
			// 	console.log("formStage return error", response?.error)
			// }
		}
		catch (e) {
			console.log("handleSaveFormedStage catch error", e)
		}
	}

	const handleParticipantsList = () => {
		setShowParticipantsList(prev => !prev)
	}

	const handlePlayerInList = (n, index) => {
		setCtx({ ...ctx, chosenPlayerInlist: n, playerIndex: index })
	}

	const handleAddGroup = (levelIndex) => {
		setCtx({ chosenPlayerInlist: "" })
		const groupName = generateGroupName(stageLevels)

		dispatch(addGroup({
			levelIndex,
			group: {
				"order_number": stageLevels?.[levelIndex]?.groups?.length + 1,
				"name": groupName,
				"type": "round_robin",
				"participants_count": 0,
				"incoming_nodes_count": 0,
				"outgoing_nodes_count": 0,
				"nodes": [],
				"matches": []
			}
		}))
	}

	const handleAddLevel = () => {
		setCtx({ chosenPlayerInlist: "" })
		const groupName = generateGroupName(stageLevels)

		dispatch(addLevel(
			{
				"order_number": stageLevels?.length + 1,
				"name": null,
				"groups": [
					{
						"order_number": 1,
						"name": groupName,
						"type": "round_robin",
						"participants_count": 0,
						"incoming_nodes_count": 0,
						"outgoing_nodes_count": 0,
						"nodes": [],
						"matches": []
					}
				]
			}
		))
	}

	const handleRemoveLevel = (levelIndex) => {
		setCtx({ chosenPlayerInlist: "" })
		const newLevels = stageLevels?.filter((lvl, index) => index !== levelIndex)
		let sortedLevels = newLevels?.sort((a, b) => a.order_number - b.order_number)
			.map((group, index) => ({ ...group, order_number: index + 1 }))

		if (sortedLevels < 1) {
			sortedLevels = [{
				"order_number": 1,
				"name": null,
				"groups": [
					{
						"order_number": 1,
						"name": "A",
						"type": "round_robin",
						"participants_count": 0,
						"incoming_nodes_count": 0,
						"outgoing_nodes_count": 0,
						"nodes": [],
						"matches": []
					}
				]
			}]
		}

		dispatch(removeLevel(sortedLevels))
	}

	const handleRemoveStage = async () => {
		try {
			for (let i = props?.stageNumber - 1; i < props?.tournament?.stages_count; i++) {
				const response = await removeStage({
					community_uid: communityUid,
					tournament_uid: tournamentUid
				}).unwrap()

				if (response?.error) {
					console.log("Remove stage return error", response?.error)
				}
			}

			setEditingMode(false)
			setCtx({ ...ctx, removeStage: true })
		}
		catch (e) {
			console.log("handleRemoveStage stage catch error", e)
		}
	}

	return (
		<>
			<div className="stage-form__wrapper">
				<div className="stage-form__container">

					<div className="stage-form__button-container">
						{
							editingMode ?
								<CloseIcon className="stage-form__button-edition-mode" onClick={() => setEditingMode(false)} />
								:
								<EditBlueIcon className="stage-form__button-edition-mode" onClick={() => setEditingMode(true)} />
						}
					</div>

					<div className="stage-form__participants-list">
						<div className="stage-form__participants-list-header" onClick={handleParticipantsList}>
							<p className="stage-form__participants-list-title">{t("Participants")} ({nodesList?.length})</p>

							<ArrowDown className={showParticipantsList ? "arrow--up" : "arrow--down"} />
						</div>

						<SmoothCollapse expanded={showParticipantsList}>
							<div className="stage-form__participants-list-container">
								{
									nodesList?.map((n, index) =>
										tournamentState?.type === "singles" ?
											<p
												key={index}
												className={index === ctx?.playerIndex ?
													"stage-form__participants-list-player--chosen"
													:
													"stage-form__participants-list-player"
												}
												onClick={() => handlePlayerInList(n, index)}
												style={{ backgroundColor: n?.placed ? "#EBF4FF" : "transparent" }}
											>
												{n?.name ? `(${n?.rating}) ${n?.name}` : "(-) ---"}
											</p>
											:
											<div
												key={index}
												className={index === ctx?.playerIndex ?
													"stage-form__participants-list-doubles--chosen"
													:
													"stage-form__participants-list-doubles"
												}
												onClick={() => handlePlayerInList(n, index)}
												style={{ backgroundColor: n?.placed ? "#EBF4FF" : "transparent" }}
											>
												<p className="stage-form__participants-list-doubles-raiting">({n?.rating})</p>

												<div>
													<p className="stage-form__participants-list-doubles-name">{n?.firstPlayerName}</p>
													<p className="stage-form__participants-list-doubles-name">{n?.secondPlayerName}</p>
												</div>
											</div>
									)
								}
							</div>
						</SmoothCollapse>
					</div>

								<div>
									
								</div>
					<DragDropContext onDragEnd={(result) => onDragEnd(result)}>
						{
							stageLevels?.map((level, levelIndex) =>
								<div key={levelIndex} className="stage-form__level">
									{
										stageLevels?.length > 1 &&
										<div className="stage-form__level-header">
											<p className="stage-form__level-name">{t("Level")} {level?.order_number}</p>

											{
												editingMode &&
												<Bucket className="delete-icon" onClick={() => handleRemoveLevel(levelIndex)} />
											}
										</div>
									}

									<div className="stage-form__group-container">
										{
											level?.groups?.map((group, groupIndex) =>
												<div key={groupIndex} className="stage-form__group">
													{
														group.type !== "olympic_plus" ?
															<RoundRobin
																group={group}
																levelIndex={levelIndex}
																groupIndex={groupIndex}
																tournament={props?.tournament}
																editingMode={editingMode}
																nodesList={nodesList}
																levels={stageLevels}
															/>
															:
															<OlympicPlus
																group={group}
																levelIndex={levelIndex}
																groupIndex={groupIndex}
																tournament={props?.tournament}
																editingMode={editingMode}
																nodesList={nodesList}
																levels={stageLevels}
															/>
													}
												</div>
											)
										}
									</div>


									{
										editingMode &&
										<ButtonStructure
											title={t("Add group")}
											onClick={() => handleAddGroup(levelIndex)}
										/>
									}
								</div>
							)
						}
					</DragDropContext>

					{
						editingMode &&
						<div className="stage-form__button-level-addition">
							<ButtonStructure
								title={t("Add level")}
								onClick={() => handleAddLevel()}
							/>
						</div>
					}

					{showSaveButton &&
						<div className="stage-form__button">
							<Button onClick={() => handleSaveFormedStage()} title={t("Save")} />
						</div>
					}

						<p
						onClick={handleRemoveStage}
						className="stage-form__delition"
						style={{marginTop: showSaveButton ? "100px" : "30px"}}
						>
							{t("Remove stage")}
							</p>
				</div>
			</div>
		</>
	)
}

export default StageForm