import React from "react"
import "./BottomMenu.css"
import { useNavigate } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { ReactComponent as Home } from "../../icon/home.svg"
import { ReactComponent as Account } from "../../icon/account.svg"

export default function BottomMenu() {
  const { t } = useTranslation()
	const authorized = localStorage.getItem("authorized")
	const communityUid = localStorage.getItem("communityUid")
	const navigate = useNavigate()

	const goToMainPage = () => {
		let mainLink = null

		if(authorized === "true") {
			mainLink = `/communities/${communityUid}/tournaments`
		} 
		else {
			mainLink = "/"
		}

		navigate(mainLink)
	}

	const goToAccountPage = () => {
		let link = null

		if(authorized === "true") {
			link = "/account"
		} 
		else {
			link = "/login-options"
		}

		navigate(link)
	}

	return (
		<div className="bottom-menu__container">
			<div onClick={goToMainPage} className="bottom-menu__link">
				<Home className="icon-home" />
				
				<p className="bottom-menu__title">
					{t("Home")}
				</p>
			</div>
			
			<div onClick={goToAccountPage} className="bottom-menu__link">
        <p className="bottom-menu__title">
					{t("Account")}
				</p>
        
				<Account className="icon-account" />
			</div>
		</div>
	)
}