import "./TournamentInfo.css"
import { useTranslation } from "react-i18next"
import { useState } from "react"
import { useUpdateTournamentMutation } from "../../redux/singleTournamentService/singleTournamentApiSlice"
import { useParams } from "react-router-dom"
import { ReactComponent as Edition } from "../../icon/edit_blue.svg"
import { ReactComponent as Close } from "../../icon/close.svg"
import Textarea from "../../components/Textarea/Textarea"
import Button from "../../components/Button/Button"
import TournamentForm from "../../pages/TournamentForm/TournamentForm"

const TournamentInfo = (props) => {
	const { t } = useTranslation()
	const [updateTournament] = useUpdateTournamentMutation()
	const {communityUid, tournamentUid} = useParams()

	const [openedEdition, setOpenedEdition] = useState(false)
	const [description, setDescription] = useState(props?.tournament?.description || "")

	const handleInfoEdition = (e) => {
		setDescription(e.target.value)
	}

	const handleSubmit = async () => {
		const tournament = {...props?.tournament}
		const body = {...tournament, description: description}

		try {
			const response = await updateTournament({ community_uid: communityUid, tournament_uid: tournamentUid, body })
			setOpenedEdition(false)
		} 
		catch (e) {
			console.log("TournamentInfo submit catch error", e)
		}
	}

	return (
		<>
			<div className="tournament-info">
				<div className="tournament-info__header">
					<p className="tournament-info__header-title">{t("Information")}</p>
					
					{
						!openedEdition ?
							<div>
								<Edition className="tournament-info__header-edition" onClick={() => setOpenedEdition(true)} />
							</div>
						:
							<Close className="close-icon" onClick={() => setOpenedEdition(false)} />
					}
				</div>

				<TournamentForm 
					header={false}
					openedEdition={openedEdition}
					tournament={props?.tournament}
				/>

				{
					openedEdition && 
						<div className="tournament-info__button-submit">
							<Button title={t("Save")} onClick={handleSubmit} />
						</div>
				}
			</div>
		</>
	)
}

export default TournamentInfo
