import "./Home.css"
import { useEffect } from "react"
import { useNavigate } from "react-router-dom"
import { useTranslation } from "react-i18next"
// import TournamentsList from "../../components/TournamentsList/TournamentsList"
import Header from "../../components/Header/Header.js"
import BottomMenu from "../../components/BottomMenu/BottomMenu"

const Home = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const authorized = localStorage.getItem("authorized")

  console.log(localStorage)

  useEffect(() => {
    if(authorized === "true"){
      navigate("/communities")
    }
  }, [authorized])

  const goLogin = () => { navigate("/login-options") }

  return (
    <>
      <Header
        className="header-container"
        backButton={false}
        subtitle={t("Tournaments")}
      />

      <div className="home__viwer-container">
        <p className="home__viwer-title" onClick={goLogin}>
          <span className="home__viwer-title--blue">{t("Login")}</span> {t("to your account to view tournaments")}
        </p>

        {/* <TournamentsList list={data} /> */}
        
        <p className="home__info-description">{t("  ")}</p>
      </div>

      <BottomMenu />
    </>
  )
}

export default Home
