import "./RoundRobinGrid.css"
import React, { useState, useEffect } from "react"
import { useTranslation } from "react-i18next"
import useCtx from "../../redux/context/useCtx"

const RoundRobinGrid = (props) => {
  const { t } = useTranslation()
  const { ctx, setCtx } = useCtx()
  //      
  const [grid, setGrid] = useState([])
  const [gridResults, setGridResults] = useState([])

  // useEffect   
  useEffect(() => {
    if (Object.keys(props.group).length !== 0) {
      //     
      const gridArr = props.group.nodes.flatMap(node => {
        if (node.double_uid === null && node.player_uid === null) {
          if (node.type === "incoming") {
            return node
          }
        }
        else {
          if (props.tournamentState.players !== null) {
            // 
            return props.tournamentState.players.filter(player => {
              return node.type === "incoming" && player.uid === node.player_uid
            })
          } 
          else {
            // 
            return props.tournamentState.doubles.filter(doublesPlayer => {
              return node.type === "incoming" && doublesPlayer.uid === node.double_uid
            })
          }
        }
      }).filter(player => player !== undefined)

      //      
      const gridResultArr = props.group.results.flatMap((result, index) => {
        return result
      })

      setGrid(gridArr)
      setGridResults(gridResultArr)
    }
    else {
      return
    }
  }, [props.tournamentState, props.group])

  //   
  function getNodes() {
    const nodesMap = new Map()

    props?.group.nodes.map(node => {
      nodesMap.set(node.uid, node)
    })

    return nodesMap
  }

  //      uid
  function getPlayerUid(index) {
    const nodes = getNodes()

    return [...nodes.keys()][index]
  }

  //       
  function getMatchPlusInverted(index1, index2) {
    const matches = props?.group?.matches
    let inverted = Boolean
    let findeMatch = []

    findeMatch = matches.filter(match => match.side2_uid === getPlayerUid(index1) && match.side1_uid === getPlayerUid(index2))

    if (findeMatch.length === 0) {
      //    ,    ,   index1  index2
      findeMatch = matches.filter(match => match.side1_uid === getPlayerUid(index1) && match.side2_uid === getPlayerUid(index2))

      if (findeMatch.length !== 0) {
        //       ,    inverted = true
        inverted = true
      }
      else {
        //      ,    goToMatch   0:0
        inverted = false

        return [
          matches,
          inverted
        ]
      }
    }
    else {
      //  ,    inverted = false
      inverted = false
    }

    return [
      findeMatch,
      inverted
    ]
  }

  //      
  function getMatch(index1, index2) {
    const [matches, inverted] = getMatchPlusInverted(index1, index2)

    return setString(matches, inverted)
  }

  const goToMatch = (matchUid) => {
    setCtx({ ...ctx, match: true, matchUid: matchUid })
    sessionStorage.setItem("match", true)
    sessionStorage.setItem("matchUid", matchUid)
  }

  //    
  function setString(matches, inverted) {
    //      
    const sets = matches.map(match => {
      return match.sets
    }).flat()

    // ,   
    if (sets.length === 0) {
      return <div>
        {
          matches?.map((match, matchIndex) => {
            return <p onClick={() => goToMatch(match.uid)} key={matchIndex}>
              <span>0:0</span>
            </p>
          })
        }
      </div>
    }
    else {
      //   
      return <div>
        {
          matches?.map((match) => {
            return match.sets.map((set, setIndex) => {
              const tieBreakScore1 = set.tie_break_score1 ? set.tie_break_score1.toString() : ""
              const tieBreakScore2 = set.tie_break_score2 ? set.tie_break_score2.toString() : ""

              if (inverted) {
                return <p key={setIndex} onClick={() => goToMatch(match.uid)}>
                  <span>{set.score1}<sup>{tieBreakScore1}</sup>:{set.score2}<sup>{tieBreakScore2}</sup></span>
                </p>
              }
              else {
                return <p key={setIndex} onClick={() => goToMatch(match.uid)}>
                  <span>{set.score2}<sup>{tieBreakScore2}</sup>:{set.score1}<sup>{tieBreakScore1}</sup></span>
                </p>
              }
            })
          })
        }
      </div>
    }
  }

  return (
    <>
      <div className="roundrobin-grid__container">
        <p className="roundrobin-grid__title">
          {t("Group")} {props?.group?.name}
        </p>
      </div>

      <div className="roundrobin-grid__wrapper">
        <div className="roundrobin-grid__sheet">
          <div className="roundrobin-grid__content">
            <div className="roundrobin-grid__row">
              <div className="roundrobin-grid__header"></div>
              <div className="roundrobin-grid__header">{t("Players")}</div>

              {
                grid?.map((grid, index) => {
                  return <div className="roundrobin-grid__header" key={index}>{index + 1}</div>
                })
              }

              <div className="roundrobin-grid__header">M</div>
              <div className="roundrobin-grid__header">S</div>
              <div className="roundrobin-grid__header">G</div>
              <div className="roundrobin-grid__header">R</div>
            </div>

            {
              grid?.map((item, indexVertical) => {
                return <div className="roundrobin-grid__row" key={indexVertical}>
                  <div className="roundrobin-grid__cell">{indexVertical + 1}</div>

                  {
                    item.player1 ?
                      // 
                      <div className="roundrobin-grid__cell">
                        {item.player1?.formedName ? `${item.player1?.formedName} / ${item.player2?.formedName}` : item.name}
                      </div>
                    :
                      // 
                      <div className="roundrobin-grid__cell">{item?.formedName ? `${item?.formedName}` : item?.name}</div>
                  }

                  {
                    grid?.map((item, indexHorizontally) => {
                      return <div className={`roundrobin-grid__cell ${indexVertical === indexHorizontally ? "roundrobin-grid__cell--grey" : ""}`} key={indexHorizontally}>
                        {
                          indexVertical === indexHorizontally ? ""
                          : 
                            getMatch(indexVertical, indexHorizontally, props?.groupIndex)
                        }
                      </div>
                    })
                  }

                  <div className="roundrobin-grid__cell">
                    {gridResults[indexVertical]?.match_points_won}  {gridResults[indexVertical]?.match_points_lost}
                  </div>
                  <div className="roundrobin-grid__cell">
                    {gridResults[indexVertical]?.set_points_won}  {gridResults[indexVertical]?.set_points_lost}
                  </div>
                  <div className="roundrobin-grid__cell">
                    {gridResults[indexVertical]?.game_points_won}  {gridResults[indexVertical]?.game_points_lost}
                  </div>
                  <div className="roundrobin-grid__cell">
                    {gridResults[indexVertical]?.place}
                  </div>
                </div>
              })
            }
          </div>
        </div>
      </div>
    </>
  )
}

export default RoundRobinGrid