import "./LoginOptions.css"
import { useTranslation } from "react-i18next"
import { useNavigate } from "react-router-dom"
import { useState } from "react"
import Header from "../../components/Header/Header.js"
import { ReactComponent as Phone } from "../../icon/phone.svg"
import { ReactComponent as Email } from "../../icon/email.svg"
import { ReactComponent as LoginIcon } from "../../icon/auth-login.svg"
import { ReactComponent as Vk } from "../../icon/vk.svg"
import CommunityLanguage from "../../components/CommunityLanguage/CommunityLanguage"

export default function LoginOptions() {
	const { t } = useTranslation()
	const navigate = useNavigate()

	const setEmailLoginType = () => {
		sessionStorage.setItem("loginType","email")
		navigate("/login")
	}

	const setPhoneLoginType = async () => {
		sessionStorage.setItem("loginType", "tel")
		navigate("/login")
	}

	const setLoginTypeText = () => {
		sessionStorage.setItem("loginType", "text")
		navigate("/login")
	}

	// do login with vkontakte. need to have website domain and to make app in vk service

	const [openedTranslator, setOpenedTranslator] = useState(false)

  const handleTranslator = () => {
    setOpenedTranslator(true)
  }

	return (
		<>
			<Header
				className="header-container"
				title={t("Login")}
				translation={true}
				backButton={true}
				openTranslator={() => handleTranslator()}
			/>

			<div className="options__container">
				<div className="options__wrapper">
					<div onClick={setPhoneLoginType} className="list__item">
						<Phone className="item__icon" />

						<p className="item__link">{t("With phone number")}</p>
					</div>

					<div onClick={setEmailLoginType} className="list__item">
						<Email className="item__icon" />

						<p className="item__link">{t("With email")}</p>
					</div>

					<div onClick={setLoginTypeText} className="list__item">
						<LoginIcon className="item__icon" />

						<p className="item__link">{t("With login")}</p>
					</div>

					<div className="list__item">
						<Vk className="item__icon" />
						
						<p className="item__link">{t("  ")}</p>
					</div>
				</div>
			</div>

			{
        openedTranslator &&
        	<CommunityLanguage onClose={() => setOpenedTranslator(false)} />
      }
		</>
	)
}