import { generateRandomValue } from "../../generalExternalFunctions"

export const uniteNodesInPairs = (nodes) => {
  const filtredNodes = nodes?.filter(n => n?.type !== "outgoing")

  return filtredNodes?.reduce((pairs, obj, index) => {
    if (index % 2 === 0) {
      pairs.push({
        match: false,
        nodeOne: obj,
        nodeTwo: nodes[index + 1],
        roundNumber: obj?.roundNumber
      })
    }
		
    return pairs
  }, [])
}

export const addEmptyPlayerInList = (list, type) => {
	const updatedList = [...list]
		updatedList.unshift({
			placed: false,
			name: "---",
			nodeUid: generateRandomValue(),
			orderNumber: 0,
			playerUid: null,
			rating: 0
		})

	return updatedList
}