import "./TournamentForm.css"

import { useState, useEffect } from "react"
import { useTranslation } from 'react-i18next'
import { useNavigate, useParams } from "react-router-dom"
import { useGetCommunityQuery, useAddTournamentMutation } from "../../redux/communityService/communityApiSlice"
import dayjs from "dayjs"
import customParseFormat from "dayjs/plugin/customParseFormat"

import Select from "../../components/Select/Select"
import Input from "../../components/Input/Input"
import Button from "../../components/Button/Button"
import Textarea from "../../components/Textarea/Textarea"
import Header from "../../components/Header/Header"

const formArrayOfObjectsForSelect = (data) => {
  const arrayOfObjects = data?.map(el => {
    return {
      label: el?.name,
      value: el?.uid
    }
  })
  return arrayOfObjects
}

export default function NewTournament(props) {
  dayjs.extend(customParseFormat)
  const { t } = useTranslation()
  const navigate = useNavigate()
  const [addTournament] = useAddTournamentMutation()
  const { communityUid } = useParams()
  const { data = {}, error, isLoading } = useGetCommunityQuery({ community_uid: communityUid })

  const [categories, setCategories] = useState([])
  const [venues, setVenues] = useState([])
  const [tournamentName, setTournamentName] = useState("")
  const [tournamentVenue, setTournamentVenue] = useState("")
  const [tournamentDate, setTournamentDate] = useState("")
  const [tournamentCategory, setTournamentCategory] = useState("")
  const [tournamentType, setTournamentType] = useState("singles")
  const [tournamentDescription, setTournamentDescription] = useState("")
  const [roundRobinRankingSystem, setRoundRobinRankingSystem] = useState("group_sets_games_win_percentage")
  const [gender, setGender] = useState("men")
  const [errName, setErrName] = useState(false)
  const [errDate, setErrDate] = useState(false)
  const [errVenue, setErrVenue] = useState(false)
  const [errCategory, setErrCategory] = useState(false)
  const [err, setErr] = useState(false)
  const [tournamentategory, setTournamentategory] = useState('')
  const [tournamentVenues, setTournamentVenues] = useState('')

  useEffect(() => {
    if (Object.keys(data).length === 0) {
      return
    } else {
      setCategories(formArrayOfObjectsForSelect(data?.categories))
      setVenues(formArrayOfObjectsForSelect(data?.venues))

      //   
      const tournamentategory = data?.categories?.find(category => {
        return category.uid === props?.tournament?.category_uid
      })
      setTournamentategory(tournamentategory)

      //   () 
      const tournamentVenues = data?.venues?.find(venue => {
        return venue.uid === props?.tournament?.venue_uid
      })
      setTournamentVenues(tournamentVenues)
    }
  }, [data, props?.tournament])

  if (isLoading) { return <h1>Loading</h1> }
  if (error) { console.log(error) }

  const checkFields = () => {
    let err = false

    if (!tournamentName) {
      setErrName(true)
      err = true
    }

    if (!tournamentVenue) {
      setErrVenue(true)
      err = true
    }

    if (!tournamentDate) {
      setErrDate(true)
      err = true
    }

    if (!tournamentCategory) {
      setErrCategory(true)
      err = true
    }

    if (err) {
      return false
    } else {
      return true
    }
  }

  const handleSubmit = async (event) => {
    event.preventDefault()

    if (!checkFields()) {
      return
    }

    const body = {
      "language_code": data?.language_code,
      "name": String(tournamentName),
      "type": tournamentType,
      "gender": gender,
      "category_uid": tournamentCategory,
      "venue_uid": tournamentVenue,
      "start_date": tournamentDate,
      "description": String(tournamentDescription),
      "community_uid": data?.uid,
      "round_robin_ranking_system": roundRobinRankingSystem
    }

    try {
      const response = await addTournament(body).unwrap()

      setTournamentName("")
      setTournamentVenue("")
      setTournamentDate("")
      setTournamentCategory("")
      setTournamentType("singles")
      setTournamentDescription("")
      setRoundRobinRankingSystem("group_sets_games_win_percentage")
      setGender("men")

      if (response?.status === 200) {
        navigate(-1)
      }

      if (response?.error?.status === 401) {
        navigate("/login-options")
      } else if (response?.error) {
        setErr(true)
        return
      }
    } catch (e) {
      console.log("addTournaments catched error", e)
      setErr(true)
    }
  }

  console.log(data)

  return (
    <>
      {
        props?.header &&
          <Header
            backButton={true}
            title={data?.name}
            subtitle={t("Tournament creation")}
          />
      }

      {
        props?.openedEdition ?
          <div className="tournamentform-wrapper">
            <div className="tournamentform-container">
              <form onSubmit={handleSubmit} className="tournamentform-form">
                <div className="form-input">
                  <label className="input__label">
                    {t("Name")}
                  </label>

                  <Input
                    onChange={(e) => setTournamentName(e.target.value)}
                    onClick={() => setErrName("")}
                    type="text"
                    borderColor={errName ? "#F20505" : ""}
                    required={false}
                  />
                  
                  {
                    errName && 
                      <p className="tournamentform__error">{t("Enter name")}</p>
                  }
                </div>

                <div className="form-select">
                  <label className="select__label">
                    {t("Venue")}
                  </label>

                  <Select
                    onChange={(choice) => setTournamentVenue(choice.value)}
                    onClick={() => setErrVenue("")}
                    options={venues}
                    err={errVenue}
                  />

                  {
                    errVenue && 
                      <p className="tournamentform__error">{t("Choose venue")}</p>
                  }
                </div>

                <div className="form-input">
                  <label className="input__label">
                    {t("Tournament start date")}
                  </label>

                  <input
                    value={tournamentDate}
                    onChange={(e) => setTournamentDate(e.target.value)}
                    onClick={() => setErrDate(false)}
                    type="date"
                    className="form__input-date"
                    style={{ border: errDate ? "1px solid red" : "" }}
                  />

                  {
                    errDate && 
                      <p className="tournamentform__error">{t("Enter date")}</p>
                  }
                </div>

                <div className="form-select">
                  <label className="select__label">
                    {t("Tournament category")}
                  </label>

                  <Select
                    onChange={(choice) => setTournamentCategory(choice.value)}
                    onClick={() => setErrCategory(false)}
                    options={categories}
                    err={errCategory}
                  />

                  {
                    errCategory && 
                      <p className="tournamentform__error">{t("Choose category")}</p>
                  }
                </div>

                <div className="form__inputs-radio">
                  <p className="inputs-radio__title">{t("Type")}</p>

                  <div className="inputs-radio__container">
                    <input
                      id="radio-single"
                      type="radio"
                      name="type"
                      value="singles"
                      defaultChecked
                      onChange={() => setTournamentType("singles")}
                      className="inputs-radio__button"
                    />
                  
                    <label htmlFor="radio-single">
                      {t("Single tournament")}
                    </label>
                  </div>

                  <div className="inputs-radio__container">
                    <input
                      id="radio-double"
                      type="radio"
                      name="type"
                      value="doubles"
                      onChange={() => setTournamentType("doubles")}
                      className="inputs-radio__button"
                    />
                  
                    <label htmlFor="radio-double">
                      {t("Double tournament")}
                    </label>
                  </div>
                </div>

                <div className="form__inputs-radio">
                  <p className="inputs-radio__title">{t("Gender")}</p>

                  <div className="inputs-radio__container">
                    <input
                      id="radio-men"
                      type="radio"
                      name="gender"
                      value="men"
                      defaultChecked
                      onChange={() => setGender("men")}
                      className="inputs-radio__button"
                    />
                    
                    <label htmlFor="radio-men">
                      {t("Men")}
                    </label>
                  </div>

                  <div className="inputs-radio__container">
                    <input
                      id="radio-women"
                      type="radio"
                      name="gender"
                      value="women"
                      onChange={() => setGender("women")}
                      className="inputs-radio__button"
                    />
                    
                    <label htmlFor="radio-women">
                      {t("Women")}
                    </label>
                  </div>

                  {
                    tournamentType === "singles" &&
                      <div className="inputs-radio__container">
                        <input
                          id="radio-other"
                          type="radio"
                          name="gender"
                          value="other"
                          onChange={() => setGender(null)}
                          className="inputs-radio__button"
                        />

                        <label htmlFor="radio-other">
                          {t("Other")}
                        </label>
                      </div>
                  }

                  {
                    tournamentType === "doubles" &&
                      <div className="inputs-radio__container">
                        <input
                          id="radio-mix"
                          type="radio"
                          name="gender"
                          value="mixed"
                          onChange={() => setGender("mixed")}
                          className="inputs-radio__button"
                        />
                        
                        <label htmlFor="radio-mix">
                          {t("Mixed")}
                        </label>
                      </div>
                  }
                </div>

                <div className="form-select">
                  <label className="select__label">
                    {t("Tournament description")}
                  </label>

                  <Textarea
                    value={tournamentDescription}
                    onChange={(e) => setTournamentDescription(e.target.value)}
                    height={"150px"}
                  />
                </div>

                <div className="form__block-addition">
                  <p className="form__block-addition__title">{t("Additional settings")}</p>

                  <div className="block-addition__container">
                    <p className="block-addition__container-title">
                      {t("Method for determining places in a circular group with equal points")}
                    </p>

                    <div className="inputs-radio__container">
                      <input
                        defaultChecked
                        id="radio-persentage--first"
                        name="percentage"
                        type="radio"
                        value={"group_sets_games_win_percentage"}
                        onChange={(e) => setRoundRobinRankingSystem(e.target.value)}
                        className="inputs-radio__button"
                      />
                      
                      <label htmlFor="radio-persentage--first">
                        {t("By the percentage of all sets/games won")}
                      </label>
                    </div>

                    <div className="inputs-radio__container">
                      <input
                        id="radio-persentage--second"
                        name="percentage"
                        type="radio"
                        value={"equal_match_points_sets_games_win_percentage"}
                        onChange={(e) => setRoundRobinRankingSystem(e.target.value)}
                        className="inputs-radio__button"
                      />
                      
                      <label htmlFor="radio-persentage--second">
                        {t("By the percentage of sets/games won in games with each other")}
                      </label>
                    </div>
                  </div>

                  {
                    err && 
                      <p className="tournamentform__main-error">
                        {t("Something went wrong. Try later please")}
                      </p>
                  }

                  {
                    props?.header &&
                      <div className="form-submit">
                        <Button type={"submit"} title={t("Create")} />
                      </div>
                  }

                </div>
              </form>
            </div>
          </div>
        :
          <div className="form-description__container">
            <p className="form-description__text">
              { tournamentVenues?.address_string }
            </p>

            <p className="form-description__text">
              <span className="form-description__bold"> :</span> { props?.tournament?.start_date }
            </p>

            <p className="form-description__text">
              <span className="form-description__bold"> :</span> { tournamentategory?.name ? tournamentategory?.name : '  '}
            </p>

            <p className="form-description__text">
              <span className="form-description__bold"> :</span> { props?.tournament?.type === 'singles' ? '' : props?.tournament?.type === 'doubles' && '' }
            </p>

            <p className="form-description__text">
              <span className="form-description__bold">:</span> { props?.tournament?.gender === 'men' ? '' : props?.tournament?.gender === 'women' ? '' : props?.tournament?.gender === 'mixed' && '' }
            </p>

            <p className="form-description__text">
              { props?.tournament?.description ? props?.tournament?.description : ' ' }
            </p>

            <p className="form-description__text">
              <span className="form-description__bold"> :</span> { props?.tournament?.round_robin_ranking_system ? '    /' : props?.tournament?.round_robin_ranking_system && '   /    ' }
            </p>
          </div>
      }
    </>
  )
}