import "./Tournament.css"
import { useState, useEffect } from "react"
import { useParams, useLocation, useNavigate } from "react-router-dom"
import { useTranslation } from "react-i18next"
import useGetPlayers from "../../redux/playersService/getPlayersHook"
import { useGetTournamentQuery } from "../../redux/singleTournamentService/singleTournamentApiSlice"
import { useGetCommunityQuery } from "../../redux/communityService/communityApiSlice"
import SmoothCollapse from "react-smooth-collapse" // animation for hidding elements
import useCtx from "../../redux/context/useCtx"
import {
	formPlayersNamesInTournament,
	checkTournamentContent,
	setCategoryOfTournament
} from "./externalFunctions"
import { useDispatch } from "react-redux"
import Header from "../../components/Header/Header.js"
import TournamentParticipants from "../TournamentParticipants/TournamentParticipants"
import TournamentInfo from "../TournamentInfo/TournamentInfo"
import TournamentStructure from "../TournamentStructure/TournamentStructure"
import TournamentStanding from "../TournamentStanding/TournamentStanding"
import TournamentSchedule from "../TournamentSchedule/TournamentSchedule"
import Match from "../Match/Match"
import PlayersGrey from "../../icon/players-grey.svg"
import PlayersBlue from "../../icon/players-blue.svg"
import StructureGrey from "../../icon/structure_menu.svg"
import StructureBlue from "../../icon/structure_menu_blue.svg"
import StandingGrey from "../../icon/grids_menu.svg"
import StandingBlue from "../../icon/grids_menu_blue.svg"
import ScheduleGrey from "../../icon/sched-grey.svg"
import ScheduleBlue from "../../icon/sched-blue.svg"
import { ReactComponent as ExitGrey } from "../../icon/exit-grey.svg"
import { ReactComponent as ExitBlue } from "../../icon/exit-blue.svg"
import { ReactComponent as InfoGrey } from "../../icon/info-grey.svg"
import { ReactComponent as InfoBlue } from "../../icon/info-blue.svg"
import { ReactComponent as ArrowDown } from "../../icon/arrow-down.svg"
import { setTournamentData } from "../../redux/singleTournamentService/singleTournamentSlice"
import NoContent from "../../components/NoContent/NoContent"

const Tournament = () => {
	const { t } = useTranslation()
	const { ctx, setCtx } = useCtx()
	const navigate = useNavigate()
	const location = useLocation()
	const dispatch = useDispatch()
	const numberOfStage = sessionStorage.getItem("stageOrder")
	const menuTabNumber = sessionStorage.getItem("menuTabNumber")
	const { tournamentUid, communityUid } = useParams()
	const { data: tournament = {}, error, isLoading } = useGetTournamentQuery({ community_uid: communityUid, tournament_uid: tournamentUid }) // tournament tournament
	const communityPlayers = useGetPlayers({ community_uid: communityUid })
	const { data: community = [], error: communityError, isLoading: communityLoading } = useGetCommunityQuery({ community_uid: communityUid })

	const [tournamentCategory, setTournamentCategory] = useState("")
	const [menuTabOrder, setMenuTabOrder] = useState(menuTabNumber ? JSON.parse(menuTabNumber) : 1)
	const [stages, setStages] = useState([])
	const [stageTabOrder, setStageTabOrder] = useState(numberOfStage ? JSON.parse(numberOfStage) : 1)
	const [shownStage, setShownStage] = useState(menuTabNumber && menuTabNumber < 4 ? true : false)
	const [showMainMenu, setShowMainMenu] = useState(true)
	const [showNoContent, setShowNoContent] = useState(false)

	useEffect(() => {
		if (Object.keys(tournament).length === 0 || Object.keys(community).length === 0) {
			return
		} 
		else {
			setShowNoContent(() => checkTournamentContent(tournament, menuTabOrder))
			setStages(() => Array.from({ length: tournament?.stages_count }, (_, index) => index + 1))
			setTournamentCategory(() => setCategoryOfTournament(community, tournament))
			const tournamentWithFormedPlayersNames = formPlayersNamesInTournament(tournament)
			dispatch(setTournamentData(tournamentWithFormedPlayersNames))
		}
	}, [tournament, community])

	useEffect(() => {
		const matchStatus = sessionStorage.getItem("match")
		const stageCreation = sessionStorage.getItem("createStage")

		if (matchStatus === "true") {
			setCtx({ ...ctx, match: JSON.parse(matchStatus) || false })
		} 
		else if (stageCreation === "true") {
			setCtx({ ...ctx, createStage: JSON.parse(stageCreation) || false })
		}
	}, [ctx?.match, ctx?.createStage])

	useEffect(() => {
		if (ctx?.numberOfCreatedStage) {
			setStageTabOrder(ctx?.numberOfCreatedStage)
			setCtx({ ...ctx, numberOfCreatedStage: "" })
		}
	}, [ctx?.numberOfCreatedStage])

	useEffect(() => {
		if (ctx?.removeStage) {
			if (stages?.length > 1) {
				setStageTabOrder(prev => prev - 1)
			} 
			else {
				setShowNoContent(false)
				setStageTabOrder(1)
			}
			setCtx({ ...ctx, removeStage: false })
		}
	}, [ctx?.removeStage])

	if (error) { console.log(error) }
	if (isLoading) { return <h1>Loading</h1> }

	const handleMenuTab = (number) => {
		const isTournamentContent = checkTournamentContent(tournament, number)
		setShowNoContent(isTournamentContent)

		setMenuTabOrder(number)
		sessionStorage.setItem("menuTabNumber", number)

		if (number !== 1) {
			setCtx({ createStage: false })
			sessionStorage.setItem("createStage", false)
		}

		if (number < 4) {
			setShownStage(true)
		} 
		else if (number === 6) {
			navigate(`/communities/${localStorage.getItem("communityUid")}/tournaments`)
		} 
		else {
			setShownStage(false)
		}
	}

	const handleStageTab = (number) => {
		setStageTabOrder(number)
		sessionStorage.setItem("stageOrder", number)
		setCtx({ ...ctx, createStage: false })
		sessionStorage.setItem("createStage", false)
	}

	const handleAddStage = () => {
		if (stages?.length < 1) {
			return
		} 
		else {
			setCtx({ ...ctx, createStage: true })
			sessionStorage.setItem("createStage", true)
		}
	}

	console.log(localStorage)

	return (
		<>
			<Header
				className="header-container"
				location={location}
				title={`${tournament?.name} (${tournamentCategory || t("No category")})`}
			/>

			<div className="tournament-wrapper">
				<div className="tournament-container">
					<div className="tournament__components">
						{
							menuTabOrder === 1 && !ctx.match && stages?.length > 0 && !showNoContent &&
								<TournamentStanding stageNumber={stageTabOrder} />
						}

						{
							menuTabOrder === 2 && !ctx.match && stages?.length > 0 && !showNoContent &&
								<TournamentSchedule stageNumber={stageTabOrder} />
						}

						{menuTabOrder === 3 && !ctx.match &&
							<TournamentStructure
								communityPlayers={communityPlayers}
								stageNumber={stageTabOrder}
								tournament={tournament}
							/>
						}

						{menuTabNumber < 3 && showNoContent &&
							<NoContent menuTabOrder={menuTabOrder} tournament={tournament} />
						}

						{
							menuTabOrder === 4 && !ctx.match &&
								<TournamentParticipants communityPlayers={communityPlayers} tournament={tournament} />
						}

						{
							menuTabOrder === 5 && !ctx.match && 
								<TournamentInfo tournament={tournament} />
						}

						{
							ctx.match && 
								<Match stageNumber={stageTabOrder} />
						}
					</div>
				</div>

				{
					!ctx?.match &&
						<div className="tournament-footer__container">
							{
								shownStage &&
									<div className="tournament__stages-menu">
										{
											stages?.length > 0 &&
												<div className="stages-menu__item">
													{
														menuTabOrder === 1 &&
															<img src={StandingBlue} className="stages-menu__item-icon" alt="Standing" />
													}
													
													{
														menuTabOrder === 2 &&
															<img src={ScheduleBlue} className="stages-menu__item-icon" alt="Schedule" />
													}

													{
														menuTabOrder === 3 &&
															<img src={StructureBlue} className="stages-menu__item-icon" alt="Structure" />
													}
													
													{
														stages?.map((s, index) => (
															<p
																key={index}
																onClick={() => handleStageTab(index + 1)}
																className={stageTabOrder === index + 1 ? "tournament__stages-tab--chosen" : "tournament__stages-tab"}
															>
																{index + 1}
															</p>
														))
													}

													{
														menuTabOrder === 3 && 
															<p onClick={handleAddStage} className="button-stage-addition">+</p>
													}

													<ArrowDown className={showMainMenu ? "arrow--down" : "arrow--up"} onClick={() => setShowMainMenu(prev => !prev)} />
												</div>
										}

										{
											stages?.length === 0 && menuTabOrder === 3 &&
												<div className="tournament__stage-menu">
													<img src={StructureBlue} className="stages-menu__item-icon" alt="Structure" />
													
													<p className="tournament__stage-addition--active">+</p>
													
													<ArrowDown className={showMainMenu ? "arrow--down" : "arrow--up"} onClick={() => setShowMainMenu(prev => !prev)} />
												</div>
										}
									</div>
							}

							<SmoothCollapse expanded={showMainMenu}>
								<div className={menuTabOrder > 3 || (menuTabOrder < 3 && stages?.length < 1) ?
									"tournament__footer-menu"
									: 
									"tournament__footer-menu--opened"
								}>
									<div onClick={() => handleMenuTab(1)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 1 ?
												<img src={StandingBlue} style={{ height: "27px", width: "33px" }} alt="Standing" />
											:
												<img src={StandingGrey} style={{ height: "27px", width: "33px" }} alt="Standing" />
										}
									</div>

									<div onClick={() => handleMenuTab(2)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 2 ?
												<img src={ScheduleBlue} style={{ height: "32px", width: "31px" }} alt="Schedule" />
											:
												<img src={ScheduleGrey} style={{ height: "32px", width: "31px" }} alt="Schedule" />
										}
									</div>

									<div onClick={() => handleMenuTab(3)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 3 ?
												<img src={StructureBlue} style={{ height: "30px", width: "38px" }} alt="Structure" />
											:
												<img src={StructureGrey} style={{ height: "30px", width: "38px" }} alt="Structure" />
										}
									</div>

									<div onClick={() => handleMenuTab(4)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 4 ?
												<img src={PlayersBlue} style={{ height: "30px", width: "30px" }} alt="Players" />
											:
												<img src={PlayersGrey} style={{ height: "30px", width: "30px" }} alt="Players" />
										}
									</div>

									<div onClick={() => handleMenuTab(5)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 5 ?
												<InfoBlue className="footer-icon" />
											:
												<InfoGrey className="footer-icon" />
										}
									</div>

									<div onClick={() => handleMenuTab(6)} className={"tournament-footer__link"}>
										{
											menuTabOrder === 6 ?
												<ExitBlue className="footer-icon" />
											:
												<ExitGrey className="footer-icon" />
										}
									</div>
								</div>
							</SmoothCollapse>
						</div>
				}
			</div>
		</>
	)
}

export default Tournament