import "./Communities.css"
import { useNavigate } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { useGetCommunitiesQuery } from "../../redux/communitiesService/communitiesApiSlice"
import { useState, useEffect } from "react"
import Popup from "../../components/Popup/Popup"

const Communities = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const authorized = localStorage.getItem("authorized")
  const { data = [], error, isLoading } = useGetCommunitiesQuery()
  const [openedPopup, setOpenedPopup] = useState(false)

  useEffect(() => {
    if(!authorized){
      navigate("/login-options")
    }
  }, [authorized])

  useEffect(() => {
    if(data?.length > 0){
      if (data?.length > 1) {
        setOpenedPopup(true)
      } 
      else {
        handleCommunity(data[0]?.uid)
      }
    }
  }, [data])

  if (isLoading) return <h1>Loading...</h1>
  if (error) return console.log("Communities", error)

  const handleCommunity = (uid) => {
    localStorage.setItem("communityUid", uid)
    navigate(`/communities/${uid}/tournaments`)
  }

  return (
    <>
      {
        openedPopup &&
          <Popup title={t("Choose community")}>
            {
              data.map((el, index) => {
                return <div key={index} className="communities-item">
                  <p className="communities__name" onClick={() => handleCommunity(el?.uid)}>{el.name}</p>
                </div>
              })
            }
          </Popup>
      }
    </>
  )
}

export default Communities
