export const filterPlayersList = (communityPlayers, participants, type) => {
	// removing players who participate in tournament from community players list
	let filteredPlayersList = []

	if (type === "singles") {
		filteredPlayersList = communityPlayers?.filter(player => {
			const participantsUids = participants?.map(item => item?.uid)
			return !participantsUids?.includes(player?.uid)
		})
	} else {
		filteredPlayersList = communityPlayers.filter(player => {
			const isUnique = !participants.some(p => p.player1?.uid === player.uid || p.player2?.uid === player.uid)
			return isUnique
		})
	}
	return filteredPlayersList
}

export const formPostRequestForSingles = (participants) => {
	return participants?.filter(p => p?.new).map(p => { return p.uid })
}

export const formPostRequestForDoubles = (participants) => {
	return participants?.filter(p => p?.new).map(p => {
		return { player1: p.player1?.uid, player2: p.player2?.uid }
	})
}

export const formDeleteRequest = (tournamentParticipants, participants) => {
	return tournamentParticipants?.filter(tP => {
		return !participants?.find(p => p.uid === tP.uid)
	}).map(tP => tP.uid)
}

export const formPatchRequest = (participants) => {
	return participants?.filter(p => p?.updated).map(p => {
		return { uid: p?.uid, player1: p.player1?.uid, player2: p.player2?.uid }
	})
}

export const generateRandomUid = () => {
	const randomNumber = Math.floor(Math.random() * 100000).toString()
	const randomLetter = String.fromCharCode(65 + Math.floor(Math.random() * 26))
	const uid = `${randomLetter}${randomNumber}`
	return uid
}

