import { Routes, Route } from "react-router-dom"
import "./App.css"
import Account from "./pages/Account/Account.js"
import Home from "./pages/Home/Home.js"
import TournamentForm from "./pages/TournamentForm/TournamentForm"
import Tournaments from "./pages/Tournaments/Tournaments"
import Tournament from "./pages/Tournament/Tournament"
import Communities from "./pages/Communities/Communities"
import Categories from "./pages/Categories/Categories.js"
import Players from "./pages/Players/Players.js"
import Venues from "./pages/Venues/Venues.js"
import SettingsAccount from "./pages/SettingsAccount/SettingsAccount.js"
import Community from "./pages/Community/Community.js"
import Login from "./pages/Login/Login.js"
import LoginOptions from "./pages/LoginOptions/LoginOptions"
import Info from "./pages/Info/Info.js"
import RequireAuth from "./pages/RequireAuth/RequireAuth"
import PlayerForm from "./pages/PlayerForm/PlayerForm"
import VenuesForm from "./pages/VenuesForm/VenuesForm"
import CategoryForm from "./pages/CategoryForm/CategoryForm"

function App() {
  return (
    <div className="root-container">
      <Routes>
        <Route path="/" element={<Home />} />

        <Route element={<RequireAuth />}>
          <Route path="/communities/:communityUid/tournaments" element={<Tournaments />} />
          <Route path="/communities" element={<Communities />} />
          <Route path="/communities/:communityUid/tournaments/form" element={<TournamentForm />} />
          <Route path="/communities/:communityUid/tournaments/:tournamentUid" element={<Tournament />} />
          <Route path="/account" element={<Account />} />
          <Route path="/account/categories" element={<Categories />} />
          <Route path="/account/players" element={<Players />} />
          <Route path="/account/players/form" element={<PlayerForm />} />
          <Route path="/account/venues" element={<Venues />} />
          <Route path="/account/settings" element={<SettingsAccount />} />
          <Route path="/account/community" element={<Community />} />
          <Route path="/account/venues/form" element={<VenuesForm />} />
          <Route path="/account/category/form" element={<CategoryForm />} />
        </Route>

        <Route path="/info" element={<Info />} />
        <Route path="/login-options" element={<LoginOptions />} />
        <Route path="/login" element={<Login />} />
      </Routes>
    </div>
  )
}

export default App
