import "./StageCreation.css"
import { useState, useEffect } from "react"
import { useTranslation } from "react-i18next"
import { useNavigate, useParams } from "react-router-dom"
import { useCreateStageMutation } from "../../redux/singleTournamentService/singleTournamentApiSlice"
import useCtx from "../../redux/context/useCtx"
import calculator from "./externalFunctions"
import Button from "../../components/ButtonWhite/ButtonWhite"

export default function StageCreation(props) {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const { ctx, setCtx } = useCtx()
  const { tournamentUid, communityUid } = useParams()
  const [createStage] = useCreateStageMutation()

  const [groupType, setGroupType] = useState(1)
  const [count, setCount] = useState(0)
  const [buttonLight, setButtonLight] = useState(1)

  useEffect(() => {
    let participantsNumber = null

    if (props?.tournament?.type === "singles") {
      participantsNumber = calculator(props?.tournament?.players.length)
    } 
    else if (props?.tournament?.type === "doubles") {
      participantsNumber = calculator(props?.tournament?.doubles.length)
    }

    setCount(participantsNumber)
  }, [props?.tournament])

  const handleGroupType = (number) => {
    if (number === 1) {
      setGroupType(1)
    } 
    else if (number === 2) {
      setGroupType(2)
    }
  }

  const handleMinus = () => {
    if (count < 1) {
      return
    } 
    else {
      setCount(prev => --prev)
    }
  }

  const handlePlus = () => {
    if (count === 32) {
      return
    } 
    else {
      setCount(prev => ++prev)
    }
  }

  const handleInput = (e) => {
    if (e.target.value > 32) {
      setCount(32)
    } 
    else if (e.target.value.startsWith("0") && e.target.value.length > 1) {
      const newCount = e.target.value.replace(/0/, "")
      setCount(newCount)
    } 
    else {
      setCount(e.target.value)
    }
  }

  const handleSubmit = async (event) => {
    event.preventDefault()
    setButtonLight(1)
    let body = null

    if (groupType === 1) {
      body = {
        "generate_method": "equal",
        "groups_count": count
      }
    } 
    else if (groupType === 2) {
      body = {
        "generate_method": "unequal",
        "group1_participants_count": count
      }
    }

    try {
      const response = await createStage({ tournament_uid: tournamentUid, body }).unwrap()

      if (response?.error?.status === 401) {
        navigate("/")
      } 
      else if (response?.error) {
        console.log("authomatical creation of createStage catch error", response?.error)
      }

      setCtx({ ...ctx, createStage: false, numberOfCreatedStage: props?.tournament?.stages_count + 1 })
      sessionStorage.removeItem("createStage")
      sessionStorage.setItem("stageOrder", props?.tournament?.stages_count + 1)
    } 
    catch (e) {
      console.log("authomatical creation in stageCreation catch error", e)
    }
  }

  // const handleManualFormation = async () => {
  //   setButtonLight(2)
  //   const body = {}

  //   try {
  //     const response = await createStage({ tournament_uid: tournamentUid, body }).unwrap()

  //     if (response?.error?.status === 401) {
  //       navigate("/")
  //     } 
  //     else if (response?.error) {
  //       console.log("manual creation of createStage catch error", response?.error)

  //       return
  //     }

  //     setCtx({ ...ctx, createStage: false, numberOfCreatedStage: props?.tournament?.stages_count + 1 })
  //     sessionStorage.setItem("createStage", false)
  //     sessionStorage.setItem("stageOrder", props?.tournament?.stages_count + 1)
  //   } 
  //   catch (e) {
  //     console.log("manual formation in stageCreation catch error", e)
  //   }
  // }

  return (
    <>
      <form onSubmit={handleSubmit} className="stage-creation">
        <p className="stage-creation__participants">
          {t("Number of participants")} {props?.tournament?.players?.length || props?.tournament?.doubles?.length}
        </p>

        <div className={groupType === 1 ? "stage-creation__input--checked" : "stage-creation__input"}>
          <div className="radio-button__container">
            <input
              id="radio-1"
              type="radio"
              name="stageType"
              value="1"
              defaultChecked
            />

            <label htmlFor="radio-1" style={{ cursor: 'pointer'}} onClick={() => handleGroupType(1)}>
              {t("Equal groups")}
            </label>
          </div>
        </div>

        <div className={groupType === 2 ? "stage-creation__input--checked" : "stage-creation__input"}>
          <div className="radio-button__container">
            <input
              id="radio-2"
              type="radio"
              name="stageType"
              value="2"
            />

            <label htmlFor="radio-2" onClick={() => handleGroupType(2)} style={{ cursor: 'pointer'}}>
              {t("Unequal groups")}
            </label>
          </div>
        </div>

        <div className="stage-creation__groups">
          <label className="stage-creation__label">
            {groupType === 1 ? t("Number of groups") : t("Number of participants in the first group")}
          </label>

          <div className="stage-creation__counter">
            <button type={"button"} onClick={() => handleMinus()} className="stage-creation__button-minus">-</button>
            
            <input
              type="number"
              value={count}
              name="count"
              onChange={e => handleInput(e)}
              maxLength="2"
              className="stage-creation__counter-input"
            />

            <button type={"button"} onClick={() => handlePlus()} className="stage-creation__button-plus">+</button>
          </div>
        </div>

        <Button
          light={buttonLight === 1 ? true : false}
          className="form-submit--automatical-formation"
          type={"submit"}
          title={t("Generate Stage automatically")}
        />

        {/* <div className="form-submit--manual-formation">
          <Button
            light={buttonLight === 2 ? true : false}
            type={"button"}
            onClick={() => handleManualFormation()}
            title={t("Build the structure manually")}
          />
        </div> */}
      </form>
    </>
  )
}