export const formLevelsAndNodes = (levels, players, type, stageNumber) => {
	const formedNodesList = []
	let playersArray = []

	if (stageNumber === 1) {
		playersArray = players?.map((p, index) => {
			return { ...p, rating: index + 1 }
		})
	} 
	else {
		playersArray = players
	}

	const newLevels = levels?.map(lvl => {
		const newGroups = lvl?.groups?.map(grp => {
			const participants = grp?.nodes?.filter(node => node.type !== "outgoing" && node.type !== "middle")
			const formedParticipants = participants.map((node) => {
				let formedNode = []

				if (type === "singles") {
					const participant = playersArray?.find((p) => p.uid === node?.player_uid)

					formedNode = {
						placed: "",
						nodeUid: node?.uid,
						bye: node?.bye,
						disabled: node?.disabled,
						type: node?.type,
						playerUid: participant?.player_uid ? participant?.player_uid : null,
						orderNumber: node?.order_number,
						rating: participant?.rating ? participant?.rating : node?.name,
						name: participant?.formedName,
						groupName: grp.name
					}
				} 
				else if (type === "doubles") {
					const participant = playersArray?.find((p) => p.uid === node?.double_uid)

					formedNode = {
						nodeUid: node?.uid,
						type: node?.type,
						bye: node?.bye,
						disabled: node?.disabled,
						orderNumber: node?.order_number,
						rating: participant?.rating ? participant?.rating : node?.name,
						firstPlayerUid: participant?.player1?.uid ? participant?.player1?.uid : null,
						secondPlayerUid: participant?.player2?.uid ? participant?.player2?.uid : null,
						firstPlayerName: participant?.player1?.uid ? participant?.player1?.formedName : "",
						secondPlayerName: participant?.player2?.uid ? participant?.player2?.formedName : "",
						groupName: grp.name
					}
				}

				formedNodesList.push(formedNode)

				return formedNode
			})

			return { ...grp, nodes: formedParticipants }
		})

		return { ...lvl, groups: newGroups }
	})

	const nodes = sortNodes(formedNodesList)
	const placedNodes = definePlacedPlayers(levels, nodes)

	return { levelsWithformedGroupsNodes: newLevels, nodes: placedNodes }
}

export const sortNodes = (nodes) => {
	const sortedNodes = nodes.sort((a, b) => a.rating - b.rating)
	
	return sortedNodes
}

export const definePlacedPlayers = (levels, nodesList) => {
	let involvedPlayers = []

	levels?.map(lvl => {
		lvl?.groups?.map(grp => {
			grp?.nodes?.map(n => {
				involvedPlayers.push(n)
				return n
			})
			return grp
		})
		return lvl
	})

	const placedPlayers = nodesList?.map(n => {
		const matchedPlayer = involvedPlayers?.find(p => p?.nodeUid === n?.nodeUid || p?.uid === n?.nodeUid)
		const placed = matchedPlayer ? true : false

		return { ...n, placed: placed }
	})

	return placedPlayers
}

export const generateGroupName = (levels) => {
	const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ"

	let count = 0
	levels?.map(lvl => {
		lvl?.groups?.map(grp => {
			count++
			return grp
		})
		return lvl
	})
	return alphabet[count]
}

export const dragItems = (result, levels) => {
	const sourceLevelIndex = result.source.droppableId.split("-")[0]
	const endLevelIndex = result.destination.droppableId.split("-")[0]

	const sourceGroupIndex = result.source.droppableId.split("-")[1]
	const finishGroupIndex = result.destination.droppableId.split("-")[1]

	const movedNode = {...levels[sourceLevelIndex]?.groups[sourceGroupIndex]?.nodes[result.source.index]}
	const replacedNode = {...levels[endLevelIndex]?.groups[finishGroupIndex]?.nodes[ result.destination.index]}

	return {
		sourceLevelIndex,
		endLevelIndex,
		finishGroupIndex,
		sourceGroupIndex,
		finishPlaceIndex: result.destination.index,
		sourcePlaceIndex: result.source.index,
		movedNode,
		replacedNode
	}
}