import "./Match.css"
import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { useGetStageQuery } from "../../redux/stageService/stageApiSlice"
import { useGetMatchQuery, useSaveSetMutation } from "../../redux/matchService/matchApiSlice"
import { useUpdateMatchMutation } from "../../redux/stageService/stageApiSlice"
import useCtx from "../../redux/context/useCtx"
import { useSelector, useDispatch } from "react-redux"
import { formDoubleParticipantsObject, formSingleParticipantsObject } from "./externalFunctions"
import { setMatchData } from "../../redux/matchService/matchSlice"

import { ReactComponent as Minus } from "../../icon/minus-delete.svg"
import { ReactComponent as Plus } from "../../icon/plus-blue.svg"
import ButtonWhite from "../../components/ButtonWhite/ButtonWhite"
import { ReactComponent as Cross } from "../../icon/close.svg"

export default function Match(props) {
	const { t } = useTranslation()
	const dispatch = useDispatch()
	const { ctx, setCtx } = useCtx()
	const { tournamentUid, communityUid } = useParams()
	const [updateMatch] = useUpdateMatchMutation()
	const [saveSet] = useSaveSetMutation()
	const matchUid = sessionStorage.getItem("matchUid")
	const { data: stage = {}, error: stageError, isLoading: stageIsLoading } = useGetStageQuery({
		community_uid: communityUid,
		tournament_uid: tournamentUid,
		stageNumber: props?.stageNumber
	})
	const { data: match = {} } = useGetMatchQuery(ctx?.matchUid || matchUid)
	const tournament = useSelector(state => state.tournament.tournament)
	const matchState = useSelector(state => state.match.match)

	const [singleParticipants, setSingleParticipants] = useState({})
	const [doubleParticipants, setDoubleleParticipants] = useState({})
	const [scoresSideOne, setScoresSideOne] = useState(0)
	const [scoresSideTwo, setScoresSideTwo] = useState(0)
	const [tiebreakScoresSideOne, setTiebreakScoresSideOne] = useState(0)
	const [tiebreakScoresSideTwo, setTiebreakScoresSideTwo] = useState(0)
	const [tiebreak, setTiebreak] = useState(false)

	useEffect(() => {
		if (Object.keys(stage).length === 0 || Object.keys(match).length === 0) {
			return
		} 
		else {
			let updatedMatch = null

			if (match?.sets?.length > 0) {
				const activeSet = { ...match?.sets[match?.sets?.length - 1], isActive: true }
				const sets = [...match?.sets]
				sets.pop()
				const updatedSets = [...sets, activeSet]
				updatedMatch = { ...match, sets: updatedSets }
			}

			dispatch(setMatchData(updatedMatch || match))

			if (tournament?.type === "singles") {
				setSingleParticipants(formSingleParticipantsObject(tournament, match, stage))
			} 
			else if (tournament?.type === "doubles") {
				setDoubleleParticipants(formDoubleParticipantsObject(tournament, match, stage))
			}
		}
	}, [tournament, stage, ctx?.matchUid, match])

	if (stageError) { console.log(stageError) }
	if (stageIsLoading) { return <h1>Loading</h1> }

	const handlePlus = (player) => {
		if (player === 1 && !tiebreak) {
			setScoresSideOne(prev => prev + 1)
		} else if (player === 1 && tiebreak) {
			setTiebreakScoresSideOne(prev => prev + 1)
		} else if (player === 2 && !tiebreak) {
			setScoresSideTwo(prev => prev + 1)
		} else if (player === 2 && tiebreak) {
			setTiebreakScoresSideTwo(prev => prev + 1)
		} 
		else {
			return
		}
	}

	const handleMinus = (player) => {
		if (player === 1 && scoresSideOne !== 0) {
			if (!tiebreak) {
				setScoresSideOne(prev => prev - 1)
			} 
			else if (tiebreak && tiebreakScoresSideOne !== 0) {
				setTiebreakScoresSideOne(prev => prev - 1)
			}

		} 
		else if (player === 2 && scoresSideTwo !== 0) {
			if (!tiebreak) {
				setScoresSideTwo(prev => prev - 1)
			} 
			else if (tiebreak && tiebreakScoresSideTwo !== 0) {
				setTiebreakScoresSideTwo(prev => prev - 1)
			}
		}
	}

	const addSix = (player) => {
		if (player === 1) {
			if (!tiebreak) {
				setScoresSideOne(6)
			} 
			else {
				setTiebreakScoresSideOne(6)
			}
		} 
		else {
			if (!tiebreak) {
				setScoresSideTwo(6)
			} 
			else {
				setTiebreakScoresSideTwo(6)
			}
		}
	}

	const activateTiebreak = () => {
		if (scoresSideTwo >= 6 && scoresSideTwo >= 6) {
			setTiebreak(prev => !prev)
		} 
		else {
			return
		}
	}

	const handleAddSet = async (clear) => {
		const newSet = {
			"order_number": matchState?.sets?.length + 1,
			"score1": 0,
			"tie_break_score1": null,
			"score2": 0,
			"tie_break_score2": null
		}

		const sets = [...matchState?.sets, newSet]
		const body = {
			"is_finished": false,
			"sets": sets
		}

		try {
			const response = await saveSet({ uid: ctx?.matchUid || matchUid, body })

			setScoresSideOne(0)
			setScoresSideTwo(0)
			setTiebreakScoresSideOne(0)
			setTiebreakScoresSideTwo(0)
			setTiebreak(false)
		} 
		catch (e) {
			console.log("handleRemoveSet catched error", e)
		}
	}

	const handleRemoveSet = async () => {
		const sets = [...matchState?.sets]
		sets?.pop()

		const body = {
			"is_finished": false,
			"sets": sets
		}

		try {
			const response = await saveSet({ uid: ctx?.matchUid || matchUid, body })

			setScoresSideOne(0)
			setScoresSideTwo(0)
			setTiebreakScoresSideOne(0)
			setTiebreakScoresSideTwo(0)
			setTiebreak(false)
		} 
		catch (e) {
			console.log("handleRemoveSet catched error", e)
		}
	}

	const handleSaveSet = async () => {
		const newSet = {
			"order_number": matchState?.sets?.length + 1,
			"score1": scoresSideOne,
			"tie_break_score1": tiebreakScoresSideOne === 0 ? null : tiebreakScoresSideOne,
			"score2": scoresSideTwo,
			"tie_break_score2": tiebreakScoresSideTwo === 0 ? null : tiebreakScoresSideTwo
		}

		const sets = [...matchState?.sets]
		sets?.pop()
		sets?.push(newSet)
		const body = {
			"is_finished": true,
			"sets": sets
		}

		try {
			const response = await saveSet({ uid: ctx?.matchUid || matchUid, body })
		} 
		catch (e) {
			console.log("handleSaveSet catched error", e)
		}
	}

	const handleFinishMatch = async () => {
		setCtx({ match: false })

		const body = {
			"is_finished": true,
			"sets": matchState?.sets
		}

		try {
			const response = await updateMatch({ uid: ctx?.matchUid || matchUid, body })

			setScoresSideOne(0)
			setScoresSideTwo(0)
			setTiebreakScoresSideOne(0)
			setTiebreakScoresSideTwo(0)
			setTiebreak(false)
			setCtx({ match: false })
			sessionStorage.setItem("match", false)
		} 
		catch (e) {
			console.log("updateMatch catched error", e)
		}
	}

	const handleCloseMatch = () => {
		setScoresSideOne(0)
		setScoresSideTwo(0)
		setTiebreakScoresSideOne(0)
		setTiebreakScoresSideTwo(0)
		setTiebreak(false)
		setCtx({ match: false })
		sessionStorage.setItem("match", false)
	}

	return (
		<>
			<div className="match__wrapper">
				<div className="match__container">
					<div className="match__icon-cross">
						<Cross onClick={handleCloseMatch} />
					</div>

					<div className="match__set">
						<div className="match__set-score">
							<div className="match__set-score-players">
								<div className="match__set-players__score">
									<div className="match__set-players__score--current">
										<div className="match__set-players__names">
											<p className="match__set-players__name--first">
												{
													tournament?.type === "singles" ?
														singleParticipants?.sideOne?.name
													:
														`${doubleParticipants?.sideOne?.name}`
												}
											</p>

											<p className="match__set-players__name--second">
												{
													tournament?.type === "singles" ? 
														singleParticipants?.sideTwo?.name
													:
														`${doubleParticipants?.sideTwo?.name}`
												}
											</p>
										</div>

										<div className="match__set-players__scores">
											<p className="match__set-players__scores-item--first" style={{ color: tiebreak ? "#CCCCCC" : "#3A3A3A" }}>
												{scoresSideOne}

												{
													(tiebreak || tiebreakScoresSideOne > 0) &&
														<sup
															className="match__set-players__scores-item--extra"
															style={{ color: tiebreak ? "#3A3A3A" : "#CCCCCC" }}
														>
															{tiebreakScoresSideOne}
														</sup>
												}
											</p>

											<p className="match__set-players__scores-item--second" style={{ color: tiebreak ? "#CCCCCC" : "#3A3A3A" }}>
												{scoresSideTwo}

												{
													(tiebreak || tiebreakScoresSideTwo > 0) &&
														<sup
															className="match__set-players__scores-item--extra"
															style={{ color: tiebreak ? "#3A3A3A" : "#CCCCCC" }}
														>
															{tiebreakScoresSideTwo}
														</sup>
												}
											</p>
										</div>
									</div>
								</div>

								<div className="match__set-players__buttons">
									<div className="match__set-players__buttons-item">
										<button onClick={() => handleMinus(1)} className="match__set-players__buttons-minus">
											<Minus type="button" className="match__set-players__icon-minus" />
										</button>
										<button type="button" onClick={() => handlePlus(1)} className="match__set-players__buttons-plus">
											<Plus />
										</button>
									</div>

									<div className="match__set-players__buttons-item">
										<button type="button" onClick={() => handleMinus(2)} className="match__set-players__buttons-minus">
											<Minus className="match__set-players__icon-minus" />
										</button>
										<button type="button" onClick={() => handlePlus(2)} className="match__set-players__buttons-plus">
											<Plus />
										</button>
									</div>
								</div>
							</div>

							<div className="match__set-buttons">
								<button type="button" onClick={() => addSix(1)} className="match__set-button--six">6</button>
								<button type="button" onClick={() => addSix(2)} className="match__set-button--six">6</button>
							</div>

							<button
								className={scoresSideOne >= 6 && scoresSideTwo >= 6 ? "match__set-taibreak--active" : "match__set-taibreak"}
								onClick={activateTiebreak}
							>
								{t("Taibreak")}
							</button>

							<ButtonWhite type="button" onClick={handleSaveSet} title={t("Save score")} />
						</div>
					</div>

					<div className="match__sets">
						<div className="match__sets-table">
							{
								matchState?.sets?.length !== 0 ?
									matchState?.sets?.map((set, index) =>
										<div style={{ backgroundColor: set?.isActive ? "#EBF4FF" : "transparent" }} key={index} className="match__sets-table__sets">
											<p className="match__sets-table__sets-scores--first">
												{set?.score1}
												<sup>{set?.tie_break_score1 ? set?.tie_break_score1 : ""}</sup>
											</p>
										
											<p className="match__sets-table__sets-scores--second">
												{set?.score2}
												<sup>{set?.tie_break_score2 ? set?.tie_break_score2 : ""}</sup>
											</p>
										</div>
									)
								:
									<div style={{ backgroundColor: "#EBF4FF" }} className="match__sets-table__sets">
										<p className="match__sets-table__sets-scores--first">0</p>
										<p className="match__sets-table__sets-scores--second">0</p>
									</div>
							}

							<div className="match__sets-table__buttons">
								<button type="button" onClick={() => handleAddSet()} className="match__sets-table__buttons-set-addition">{t("Add set")}</button>
								<button type="button" onClick={handleRemoveSet} className="match__sets-table__buttons-set-deletion">{t("Remove set")}</button>
							</div>

							<button onClick={handleFinishMatch} className="match__sets-table__button-finish">{t("Finish match")}</button>
						</div>
					</div>
				</div>
			</div>
		</>
	)
}