export const getCurrentMatch = (stage, matchUid) => {
	return stage?.levels?.map(level =>
		level?.groups?.map(group =>
			group.matches.filter(match => match?.uid === matchUid)
		)
	).flat()[0][0]
}

export const formDoubleParticipantsObject = (tournament, currentMatch, stage) => {
	const sideOne = stage?.levels?.flatMap(lvl => {
		const foundNode = lvl?.groups?.flatMap(grp => {
			const node = grp?.nodes?.filter(n => {
				if (n?.uid === currentMatch?.side1_uid) {
					return true
				}

				return false
			})

			return node
		})

		return foundNode
	})[0]

	const sideTwo = stage?.levels?.flatMap(lvl => {
		const foundNode = lvl?.groups?.flatMap(grp => {
			const node = grp?.nodes?.filter(n => {
				if (n?.uid === currentMatch?.side2_uid) {
					return true
				}

				return false
			})

			return node
		})

		return foundNode
	})[0]

	const playerOne = tournament?.doubles?.filter(d => {
		return d?.uid === sideOne?.double_uid
	}).flat()[0]

	const playerTwo = tournament?.doubles?.filter(d => {
		return d?.uid === sideTwo?.double_uid
	}).flat()[0]

	return {
		sideOne: {
			uid: sideOne?.uid,
			name: playerOne ? `${playerOne?.player1?.formedName}, ${playerOne?.player2?.formedName}` : sideOne?.name
		},
		sideTwo: {
			uid: sideTwo?.uid,
			name: playerTwo ? `${playerTwo?.player1?.formedName}, ${playerTwo?.player2?.formedName}` : sideTwo?.name
		}
	}
}

