import "./ButtonWhite.css"
import { useTranslation } from "react-i18next"

export default function ButtonWhite(props) {
  const { t } = useTranslation()

  return (
    <button
      type={props?.type}
      onClick={props?.onClick}
      className={props?.light ? "button-structure__light" : "button-structure"}
    >
      {t(props?.title)}
    </button>
  )
}