import { apiSlice } from "../apiSlice"

export const stageApi = apiSlice.injectEndpoints({
  reducerPath: "stageApi",
  tagTypes: ["Stage"],
  endpoints: (build) => ({
    getStage: build.query({
      query: ({ tournament_uid, stageNumber }) => ({
        url: `tennis/tournaments/${tournament_uid}/stages/${stageNumber}/`,
        method: "GET"
      }),
      providesTags: ["Stage"]
    }),
    formStage: build.mutation({
      query: ({ tournament_uid, body }) => ({
        url: `tennis/tournaments/${tournament_uid}/stages/`,
        method: 'PATCH',
        body
      }),
      invalidatesTags: ["Stage"]
    }),
    updateMatch: build.mutation({
      query: ({uid, body}) => ({
        url: `tennis/matches/${uid}/`,
        method: 'PATCH',
        body
      }),
      invalidatesTags: ["Stage"]
    })
  })
})

export const {
  useGetStageQuery,
  useFormStageMutation,
  useUpdateMatchMutation
} = stageApi