import "./PlayerForm.css"
import { useTranslation } from "react-i18next"
import { useState } from "react"
import fieldsChecker from "form-fields-checker"
import { isValidDate } from "./externalFunctions"
import { useAddPlayerMutation, useUpdatePlayerMutation } from "../../redux/playersService/playersApiSlice"
import { useNavigate } from "react-router-dom"

import Header from "../../components/Header/Header"
import Input from "../../components/Input/Input"
import Button from "../../components/Button/Button"
import BottomMenu from "../../components/BottomMenu/BottomMenu"

const PlayerCreation = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const updatedPlayer = JSON.parse(sessionStorage.getItem("updatedPlayer"))
  const [createPlayer] = useAddPlayerMutation()
  const [updatePlayer] = useUpdatePlayerMutation()
  const communityUid = localStorage.getItem("communityUid")

  const [gender, setGender] = useState("male")
  const [name, setName] = useState(updatedPlayer?.first_name ? updatedPlayer?.first_name : "")
  const [lastName, setLastName] = useState(updatedPlayer?.last_name ? updatedPlayer?.last_name : "")
  const [middleName, setMiddle] = useState(updatedPlayer?.middle_name ? updatedPlayer?.middle_name : "")
  const [birthDate, setBirthDate] = useState(updatedPlayer?.birth_date ? updatedPlayer?.birth_date : "")
  const [birthYear, setBirthYear] = useState(updatedPlayer?.birth_year ? updatedPlayer?.birth_year : "")
  const [email, setEmail] = useState(updatedPlayer?.email ? updatedPlayer?.email : "")
  const [phone, setPhone] = useState(updatedPlayer?.mobile ? updatedPlayer?.mobile : "")

  const [nameErr, setNameErr] = useState(false)
  const [lastNameErr, setLastNameErr] = useState(false)
  const [phoneErr, setPhoneErr] = useState(false)
  const [emailErr, setEmailErr] = useState(false)
  const [birthDateErr, setBirthDateErr] = useState(false)
  const [birthYearErr, setBirthYearErr] = useState(false)
  const [err, setErr] = useState(false)

  const handlePhone = (value) => {
    setPhoneErr(t(""))

    if (value.startsWith("8")) {
      setPhone("+7" + phone?.slice(1))
    } 
    else if (value.startsWith("9")) {
      setPhone("+7" + value)
    } 
    else if (value.startsWith("7")) {
      setPhone(prev => "+7" + prev)
    } 
    else {
      setPhone(value)
    }
  }

  const handleBirthDate = (value) => {
    setBirthDate(value)

    if (isValidDate(value)) {
      const yearOfBirth = value.slice(0, 4)
      setBirthYear(yearOfBirth)
    }
  }

  const handleBirthYear = (value) => {
    setBirthYearErr("")

    if (birthDate) {
      return
    } 
    else if (value?.length > 4) {
      return
    } 
    else if (value?.length < 1) {
      setBirthYear("")
    } 
    else {
      setBirthYear(value)
    }
  }

  const handleEmail = (value) => {
    setEmailErr("")
    setEmail(value)
  }

  const validateFields = () => {
    let confirmValidation = true

    if (!name) {
      setNameErr(true)
      confirmValidation = false
    }

    if (!lastName) {
      setLastNameErr(true)
      confirmValidation = false
    }

    if (email) {
      if (fieldsChecker?.isValidEmail(email) !== true) {
        setEmailErr(true)
        confirmValidation = false
      }
    }

    if (phone) {
      if (fieldsChecker?.isValidPhone(phone, ["11"]) !== true) {
        setPhoneErr(true)
        confirmValidation = false
      }
    } else {
      setPhoneErr(true)
      confirmValidation = false
    }

    if (birthDate && !isValidDate(birthDate)) {
      setBirthDateErr(true)
      confirmValidation = false
    } else if (!birthDate) {
      setBirthDateErr(true)
      confirmValidation = false
    }

    if (birthYear && (birthYear.length < 4 || birthYear.length > 5)) {
      setBirthYearErr(true)
      confirmValidation = false
    } else if (!birthYear) {
      setBirthYearErr(true)
      confirmValidation = false
    }

    return confirmValidation
  }

  const handleSubmit = async (e) => {
    e.preventDefault()
    const isValidFields = validateFields()

    if (!isValidFields) {
      return
    }

    try {
      const body = {
        "gender": gender?.name,
        "last_name": lastName,
        "first_name": name,
        "middle_name": middleName,
        "birth_date": birthDate,
        "birth_year": birthYear,
        "email": email,
        "mobile": phone,
        "community_uids": [communityUid]
      }

      let response = null
      if (updatedPlayer) {
        response = await updatePlayer({ body, player_uid: updatedPlayer?.uid }).unwrap()
      } 
      else {
        response = await createPlayer({ body }).unwrap()
      }

      if (response?.error?.status === 401) {
        navigate("/login-options")
      } else if (response?.error) {
        setErr(true)
        return
      } else {
        navigate("/account/players")
      }
    } catch (e) {
      console.log("createPlayer catch error", e)
      setErr(true)
    }
  }

  return (
    <>
      <Header
        title={updatedPlayer ? t("Edition") : t("Add player")}
        backButton={true}
      />

      <div className="player-form__container">
        <form onSubmit={handleSubmit}>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Last name")}</label>
            <Input
              value={name}
              onChange={(e) => setName(e.target.value)}
              type="text"
              required={false}
              borderColor={lastNameErr ? "#F20505" : ""}
            />

            {lastNameErr && <p className="player-form__error">{t("Enter last name")}</p>}
          </div>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Player name")}</label>
            <Input
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
              type="text"
              required={false}
              borderColor={nameErr ? "#F20505" : ""}
            />

            {nameErr && <p className="player-form__error">{t("Enter player name")}</p>}
          </div>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Middle name")}</label>
            <Input
              value={middleName}
              onChange={(e) => setMiddle(e.target.value)}
              required={false}
              type="text"
            />
          </div>

          <div className="player-form__inputs-radio">
            <p className="inputs-radio__title">
              {t("Gender")}
            </p>

            <div className="inputs-radio__container">
              <input
                defaultChecked
                id="player-gender__male"
                name="gender"
                type="radio"
                value={"male"}
                onChange={(e) => setGender(e.target.value)}
                className="inputs-radio__button"
              />
              <label htmlFor="player-gender__male">
                {t("Male")}
              </label>
            </div>

            <div className="inputs-radio__container">
              <input
                id="player-gender__female"
                name="gender"
                type="radio"
                value={"female"}
                onChange={(e) => setGender(e.target.value)}
                className="inputs-radio__button"
              />
              <label htmlFor="player-gender__female">
                {t("Female")}
              </label>
            </div>
          </div>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Birth date")}</label>
            <Input
              value={birthDate}
              onChange={(e) => handleBirthDate(e.target.value)}
              type={"date"}
              required={false}
              borderColor={birthDateErr ? "#F20505" : ""}
            />

            {birthDate && birthDateErr && <p className="player-form__error">{t("Incorrect birth date")}</p>}
            {!birthDate && birthDateErr && <p className="player-form__error">{t("Enter birth date")}</p>}
          </div>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Birth year")}</label>
            <Input
              value={birthYear}
              onChange={(e) => handleBirthYear(e.target.value)}
              type="number"
              required={false}
              borderColor={birthYearErr ? "#F20505" : ""}
            />

            {birthYear && birthYearErr && <p className="player-form__error">{t("Incorrect birth year")}</p>}
            {!birthYear && birthYearErr && <p className="player-form__error">{t("Enter birth year")}</p>}
          </div>

          <div className="player-form__input">
            <label className="player-form__input-label">{t("Email")}</label>
            <Input
              value={email}
              onChange={(e) => handleEmail(e.target.value)}
              type={"email"}
              borderColor={emailErr ? "#F20505" : ""}
              required={false}
            />

            {emailErr && <p className="player-form__error">{t("Invalid email")}</p>}
          </div>

          <div className="player-form__input">
            <label htmlFor="phone" className="player-form__input-label">{t("Phone number")}</label>
            <Input
              value={phone}
              onChange={(e) => handlePhone(e.target.value)}
              type="tel"
              id="phone"
              borderColor={phoneErr ? "#F20505" : ""}
              required={false}
            />

            {phone && phoneErr && <p className="player-form__error">{t("Invalid phone number")}</p>}
            {!phone && phoneErr && <p className="player-form__error">{t("Enter phone number")}</p>}
          </div>

          {err && <p className="player-form__main-error">{t("Something went wrong. Try later please")}</p>}

          <div className="player-form__submit">
            <Button type={"submit"} title={t("Save")} />
          </div>
        </form>
      </div>

      <BottomMenu />
    </>
  )
}

export default PlayerCreation
